<template>
  <div>
      <v-tabsFileManagement filetypeName1="" filetypeName2="" filetypeName3="" filetypeName4="" :fileTabsData="fileTabsData" :fileTabsMoreData="fileTabsMoreData" :fileTabsStatusData="fileTabsStatusData"></v-tabsFileManagement>
      <v-tableCom :tableDataContent="tableData" :tableOptions="tableOptions" :tindex="0" :selectionCheck="1" :tagsModel="true" :projId="projIdA" :keyValuesType="keyValuesType" @delete-file="deleteSearchClick" @new-file="updataFile"></v-tableCom>
      <!-- pagination -->
      <div class="paginationNav paginationCommon">
        <div class="title">共 查询到 {{recordsTotal}} 条记录 共 {{totalPage}} 页</div>
        <el-button  @click="indexBtn">首页</el-button>
        <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        @prev-click="prevClick"
        @next-click="nextClick"
        :current-page="currentPage"
        :total="recordsTotal"
        :page-size="pageSize"
        layout="prev, pager, next"
        background
        prev-text='上一页'
        next-text='下一页'
        >
        </el-pagination>
        <el-button @click="backBtn">末页</el-button>
      </div>
  </div>
</template>
<script>
import TabsFileManagement  from "@/components/tabMenu" //投前文件管理tabs标签列表
import FileManagementTable from "@/components/fileManagementTable" //投前文件管理table
import TableCom            from '@/components/tabsFileManagementTable'
import url                 from '@/serviceAPI.config.js'
export default {
  props:{
    projId:{
      type:String,
      default:''
    },
    flag:{
      type:Boolean,
      default:false
    }
  },
  data() {
    return {
      keyValuesType:1,
      fileTabsData: ["投资意向书", "保密协议", "其他"],
      fileTabsStatusData: ["草稿", "审核中", "已审核", "已签订", "无效"],
      fileTabsMoreData: [
        "草稿",
        "审核中",
        "审核失败",
        "已拒绝",
        "审核成功",
        "已确认支付",
        "已执行",
        "已终止",
        "执行中",
        "已删除"
      ],
      menuDataObj: [
        { icon: "el-icon-circle-plus-outline", itemName: "新增" },
        { icon: "el-icon-edit", itemName: "编辑" },
        { icon: "el-icon-circle-close-outline", itemName: "删除" },
        { icon: "el-icon-circle-close-outline", itemName: "失效" },
        { icon: "el-icon-edit", itemName: "签订" },
        { icon: "el-icon-edit", itemName: "更变" }
      ],
      tableOptions: [
        { prop: "eDocName", label: "电子版", align: "left", width: "300" },
        { prop: "formVersionName", label: "正式版", align: "left", width: "300" },
        { prop: "sheetType", label: "类别", align: "left", width: "140" },
        { prop: "statusName", label: "状态", align: "left", width: "" }
      ],
      //新增参数
      newOject:{
        keyValues: '',
        tableName:'TZ_T_TERM_SHEET',
        dealMark:'new',
        sequenceName:'SEQ_TERM_SHEET',
        springJndiId:'YHKG_DS_EI',
        resultJsp:'../WEB-INF/views/fundsProjectDecision/addPreInvestFiles.jsp',
        className:'com.founder.ei.bizobject.PreInvestFilesObj',
      },
       //编辑参数
      editOject:{
        tableName:'TZ_T_TERM_SHEET',
        dealMark:'update',
        sequenceName:'SEQ_TERM_SHEET',
        springJndiId:'YHKG_DS_EI',
        resultJsp:'../WEB-INF/views/fundsProjectDecision/updatePreInvestFiles.jsp',
        className:'com.founder.ei.bizobject.PreInvestFilesObj',
      },
      //分页字段
      currentPage:1,   //默认显示第一页
      pageSize:5,      //默认每页显示5条
      tableData:[],    //table数据
      totalPage:0,     //总页码
      shouYePage:1,    //首页页码
      recordsTotal:0,   //查询记录数量
      projIdA:'',
    };
  },
  components: {
    "v-tabsFileManagement"  : TabsFileManagement,
    "v-fileManagementTable" : FileManagementTable,
    "v-tableCom"            :TableCom, //table组件
  },
  created() {
    this.projIdA = this.projId

  },
  watch: {   //获取企业信息列表
    flag(val){
      if(val){
         this.getEnteInfos(this.projId,this.pageSize,this.currentPage);
      }
    }
  },
  methods: {
    deleteSearchClick(val){//删除数据
      if(val==='2'){
        this.getEnteInfos(this.projId,this.pageSize, 1)
      }
    },
    updataFile(val){//新增更新数据
      if(val){
        this.getEnteInfos(this.projId,this.pageSize, this.currentPage)
      }
    },
    //获取投前文件列表
    getEnteInfos(projId,pageSize, currentPage) {
      let _self = this;
      var t = new Date().getTime();
      _self
        .$axios({
          url: `${url.getPreInvestFilesList}?projId=${projId}&pageSize=${pageSize}&currPage=${currentPage}&t=${t}`,
          method: "get"
        })
        .then(response => {
          if (response.data.status === '0') {
            _self.tableData = response.data.data; //获取table数据
            _self.recordsTotal = response.data.totalCount//获取查询总数
            _self.totalPage = response.data.totalPage; //获取总页码
          }
        })

    },
    // 回到首页
    indexBtn() {
      this.currentPage = this.shouYePage;
      this.getEnteInfos(this.projId,this.pageSize, this.currentPage);
    },
    // 回到末页
    backBtn() {
      this.currentPage = this.totalPage;
      this.getEnteInfos(this.projId,this.pageSize, this.currentPage);
    },
    //上一页
    prevClick(currentPage) {
      this.currentPage = currentPage
      this.getEnteInfos(this.projId,this.pageSize, currentPage);
    },
    //下一页
    nextClick(currentPage) {
       this.currentPage = currentPage
      this.getEnteInfos(this.projId,this.pageSize, currentPage);
    },
    handleSizeChange(size) {
      //pageSize 改变时会触发
      let _self = this;
      _self.pageSize = size; //每一页显示多少条
      console.log(`每页 ${size} 条`);
    },
    handleCurrentChange(currentPage) {
      //currentPage 改变时会触发  第几页
      this.getEnteInfos(this.projId,this.pageSize, currentPage);
      this.currentPage = currentPage; //第几页
    }
  }
};
</script>

<style>
/*分页-start*/
.paginationCommon {
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
}
.paginationCommon .title {
  font-size: 12px;
  font-family: "Microsoft YaHei";
  margin-right: 30px;
}
/*button 首页 末页*/
.paginationCommon .el-button {
  padding: 0 10px;
  display: inline-block;
  font-size: 13px;
  min-width: 29px;
  height: 29px;
  line-height: 29px;
  vertical-align: top;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background-color: #fff;
  color: #d1ab62;
  border: 1px solid #d1ab62;
}

paginationCommon .el-button:hover {
  color: #fff;
  background-color: #d1ab62;
}

paginationCommon .el-button:active {
  color: #fff;
  background-color: #d1ab62;
}

paginationCommon .el-button:link {
  color: #fff;
  background-color: #d1ab62;
}

.paginationCommon .el-pagination.is-background .btn-prev,
.el-pagination.is-background .el-pager li {
  background: url(../assets/images/prev_sanjiao.png) no-repeat scroll 7px center;
  border: 1px solid #d1ab62;
}

.paginationCommon .el-pagination.is-background .btn-next,
.el-pagination.is-background .el-pager li {
  background: url(../assets/images/next_sanjiao.png) no-repeat scroll 50px center;
  border: 1px solid #d1ab62;
}

.paginationCommon .el-pagination button,
.el-pagination span:not([class*="suffix"]) {
  padding: 0 10px;
}

.paginationCommon .el-pagination.is-background .el-pager li:not(.disabled).active {
  color: #fff;
  background-color: #d1ab62;
}
/*分页-end*/
</style>





