<template>
    <div class="headerTitleContainer" :style="{ paddingTop: cHeight + 'px' }">
      <el-row>
        <el-col :span="24"><div class="grid-content bg-purple-dark headerTitle">
            <div class="classify">{{valueTitle}}</div>
               <slot></slot>
            </div>
        </el-col>
      </el-row>
    </div>
</template>

<script>
export default {
  props:['valueTitle','cHeight']
}
</script>

<style scoped>
.headerTitleContainer{
    padding-bottom: 13px;
}
.headerTitle{
    display: flex;
    display: -webkit-flex ;
    align-items: center;
}
.titleBorder{
    display: inline-block;
    width: 3px;
    height: 15px;
    background-color: #804000;
}
.classify{
    background: url(../assets/images/diandian.png) no-repeat scroll left center;
    color: #804000 !important;
    font-size: 16px;
    padding-left: 11px;
}
</style>





