<template>
<div>
  <el-table
    v-loading="false"
    :data="tableDataContent"
    border
    style="width:100%;">
    <el-table-column v-if="tindex"
      type="index"
      align="center"
      width="70">
    </el-table-column>

    <el-table-column v-if="selectionCheck" type="selection" width="55" align="center"></el-table-column>
    <!-- v-for -->
    <el-table-column
       v-for="(item,index) in tableOptions"
       :key="index"
       :prop="item.prop"
       :label="item.label"
       :align="item.align"
       :width="item.width"
    >
    </el-table-column>
  </el-table>
</div>

</template>

<script>
export default {
  props:["tableDataContent","tableOptions","tindex","selectionCheck"],
  data() {
    return {}
  },
  created(){
    //这里写得不到数据，应为数据是延迟返回的
  },
  watch:{
    tableDataContent:function(val){
      console.log("val-qq--",val)
    },
    tindex:function(val){
      console.log("val---",val)
    }
  },
  methods: {
    tableheaderClassName({ row, rowIndex }) {
      return "table-head-th";
    }
  }
};
</script>
<style>
/*表头背景色 */
.el-table th {
  background: #eae3d8 !important;
  color: #997528;
}
.el-table th>.cell {
  font-size: 16px;
}
</style>



