<template>
  <div>
    <div class="table-P1-view">
      <div class="item_view">
          <div class="item">
              <div class="left">出资主体*</div>
              <div class="right">{{contentData.inveName}}</div>
          </div>
          <div class="item">
              <div class="left">拟投资金额不超过(元)*</div>
              <div class="right">{{contentData.intendedAmount}} {{contentData.intendedCurrency}}</div>
          </div>
      </div>
      <div class="editItem">
          <div class="left">拟投资比例不超过(%)</div>
          <div class="right">{{contentData.perShare}}</div>
      </div>
      <div class="editItem">
          <div class="left">决策事项* </div>
          <div class="right" v-html="str1"></div>
      </div>
      <div class="editItem">
          <div class="left">投资理由* </div>
          <div class="right" v-html="str"></div>
      </div>
      <div class="editItem">
          <div class="left">附件 </div>
          <div class="right uploadFileBox">
              <div class="uploadFileItem">
                   <p>商业计划书</p>
                   <div class="file-box">
                        <h3 v-for="(item,index) in contentData.bizPlanAttList"
                          :key="index">
                          <a :href="item.filePath" v-if="item.fileOldName!==null">{{item.fileOldName}}<span>({{item.fileSize}})</span></a>
                        </h3>
                   </div>
              </div>
              <div class="uploadFileItem">
                   <p>立项报告</p>
                   <div class="file-box">
                      <h3 v-for="(item,index) in contentData.projReportAttList"
                          :key="index">
                          <a :href="item.filePath" v-if="item.fileOldName!==null"
                          >{{item.fileOldName}}<span>({{item.fileSize}})</span>
                          </a>
                      </h3>
                   </div>
              </div>
              <div class="uploadFileItem">
                   <p>其他</p>
                   <div class="file-box">
                      <h3 v-for="(item,index) in contentData.otherAttList"
                          :key="index">
                          <a :href="item.filePath" v-if="item.fileOldName!==null"
                          >{{item.fileOldName}}<span>({{item.fileSize}})</span>
                          </a>
                      </h3>
                   </div>
              </div>
          </div>
      </div>
	</div>
  </div>
</template>

<script>
export default {
  props:{
    contentData:{
      type:Object,
      default: function () {
        return {
          inveName: '',
          perShare:'',
          intendedAmount:'',
          majorMatter:'',
          inveReason:'',
          bizPlanAttUrl:'',
          bizPlanAttName:'',
          bizPlanAttSize:'',
          projReportAttUrl:'',
          projReportAttName:'',
          projReportAttSize:'',
          otherAttUrl:'',
          otherAttName:'',
          otherAttSize:''
        }
      }
    }
  },
  computed:{
    str(){
      if(this.contentData.inveReason){
          return this.contentData.inveReason.replace(/\n/g, "<br/>")
      }
    },
    str1(){
      if(this.contentData.majorMatter){
          return this.contentData.majorMatter.replace(/\n/g, "<br/>")
      }
    }
  },
  data() {
    return {}
  },
  watch: {},
  mounted() {},
  created() {

  },
  methods: {}
};
</script>
<style scoped>
.table-P1-view {
  border: 1px solid #e9e9e9;
}
.table-P1-view .item_view {
  display: flex;
  width: 100%;
}
.table-P1-view .item_view + .item_view {
  border-top: 1px solid #e9e9e9;
}
.table-P1-view .item_view .item {
  display: flex;
  width: 50%;
}
.table-P1-view .item_view .item + .item {
  border-left: 1px solid #e9e9e9;
}
.table-P1-view .item_view .item .left {
  border-right: 1px solid #e9e9e9;
  width: 167px;
  color: #98742a;
  font-size: 14px;
  font-family: 微软雅黑;
  padding-left: 14px;
  box-sizing: border-box;
  background-color: #f4f4f4;
  display: flex;
  align-items: center;
}
.table-P1-view .item_view .item .right {
  color: #666;
  font-size: 14px;
  font-family: 微软雅黑;
  padding-left: 19px;
  box-sizing: border-box;
  width: 64%;
  padding: 16px 20px;
}
.table-P1-view .editItem {
  width: 100%;
  border-top: 1px solid #e9e9e9;
  display: flex;
}
.editItemH {
  height: 94px;
}
.table-P1-view .editItem .left {
  width: 167px;
  background-color: #f4f4f4;
  border-right: 1px solid #e9e9e9;
  color: #98742a;
  font-size: 14px;
  font-family: 微软雅黑;
  padding: 16px 0 16px 14px;
  box-sizing: border-box;
}
.table-P1-view .editItem .right {
  width: 82.3%;
  padding: 16px 20px 16px 20px;
  box-sizing: border-box;
  color: #666;
  font-size: 14px;
  font-family: 微软雅黑;
}
/*附件*/
.uploadFileBox{
  display: flex;
  padding: 0!important;
}
.uploadFileBox .uploadFileItem{
  flex: 1;
}
.uploadFileBox .uploadFileItem+.uploadFileItem{
  border-left: 1px solid #e7e7e7;
}
.uploadFileBox .uploadFileItem p{
  height: 46px;
  line-height: 46px;
  padding-left: 20px;
}
.uploadFileBox .uploadFileItem .file-box{
  border-top: 1px solid #e7e7e7;
  padding: 14px 10px 14px 20px;
  display: flex;
  flex-direction: column;
}
.uploadFileBox .uploadFileItem .file-box a{
 color: blue;
 font-size: 14px;
 font-family: 微软雅黑;
 height: 26px;
 line-height: 26px;
}

.uploadFileBox .uploadFileItem .file-box a span{
  color: #666;
  font-size: 14px;
  font-family: 微软雅黑;
}
</style>






