                  keyValues : id,
                  tableName : "XJL_T_PAYMENT_REQUEST",
                  dealMark : "update",
                  sequenceName : "SEQ_XJL_T_PAYMENT_REQUEST",
                  springJndiId: "YHKG_DS_EI",
