    //资料
    upload_click(){
      var _self = this
      var projectId = this.projId;

      console.log(">>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>.")
      console.log(this.multipleSelection)
      if(this.multipleSelection.length==0) {
        layer.msg(`请选中一条数据`,{icon:6})
      }else if(this.multipleSelection.length>1){
        layer.msg(`请选中一条数据`,{icon:6})
      }else if(this.multipleSelection[0].status!=='3'){
        layer.msg(`请选择审核通过的记录`,{icon:6})
      }else{
        var id = this.multipleSelection[0].pkId;
        this.newPageUrl = appendParams(
          `${this.url_a}/eiweb/action/bizObjectBase.action`,
          {
            keyValues :id,
            tableName : "TZ_T_FP_THREEMEETING_INFO",
            dealMark : "update",
            sequenceName : "",
            springJndiId: "YHKG_DS_EI",
            resultJsp : "../WEB-INF/views/regularMonitor/threeMettingManageUpload.jsp",
            className : "com.founder.ei.bizobject.FpThreeMettingManageUploadObj",
            inJCEdit:false,
            openMethod : "2"
          }
        );
        layerOpenWindow("上传",this.newPageUrl,'1000px', '600px',function(){
           _self.$emit('delete-search',"1")
        })
      }


    },
    //删除
    delete_click() {
        var flag = true
        if(this.multipleSelection.length==0){
          layer.msg(`请选中一条数据`,{icon:6})
          return
        }
        for(let i=0;i<this.multipleSelection.length;i++){
          if(this.keyValuesType === 2 || this.keyValuesType === 10){
            if(this.multipleSelection[i].status.indexOf('1')==-1){
              layer.msg(`只能草稿下才能删除`,{icon:6})
              flag = false
            }
          }else{
            if(this.multipleSelection[i].status.indexOf('0')==-1){
              layer.msg(`只能草稿下才能删除`,{icon:6})
              flag = false
            }
          }

        }
        if(flag){
          this.dialogVisible = true;
        }
    },
    delete_click_ack() {
      //删除请求接口
      var projectId = this.projId
      var id,keyName,tableName,id_string
      let objData = this.multipleSelection
      const delete_arr = []

      if(this.keyValuesType === 5){//退出记录
        // id = this.multipleSelection[0].appId;
        for(var i in objData) {
           delete_arr.push(objData[i].appId)
        }
        id_string = delete_arr.join(',')
        keyName='APP_ID'
        tableName='TZ_T_FUND_QUIT_APPL'
      }

      if(this.keyValuesType === 4){//投资支付
        // id = this.multipleSelection[0].pqId;
        for(var i in objData) {
           delete_arr.push(objData[i].pqId)
        }
        id_string = delete_arr.join(',')
        keyName='PQ_ID'
        tableName='XJL_T_PAYMENT_REQUEST'
      }

      if(this.keyValuesType === 3){//合同
        // id = this.multipleSelection[0].fileId;
        for(var i in objData) {
           delete_arr.push(objData[i].fileId)
        }
        id_string = delete_arr.join(',')
        keyName='FILE_ID'
        tableName='TZ_T_PROJ_CONTRACT_FILE'
      }

      if(this.keyValuesType === 2){//质量评估
        for(var i in objData) {
           delete_arr.push(objData[i].paramId)
        }
        id_string = delete_arr.join(',')
        keyName='PARAM_ID'
        tableName='SC.SC_PARAM'
      }

      if(this.keyValuesType === 10){//重大事项
        for(var i in objData) {
           delete_arr.push(objData[i].pkId)
        }
        id_string = delete_arr.join(',')
        keyName='PK_ID'
        tableName='TZ_T_FP_THREEMEETING_INFO'
      }

      let getUrl = `${url.getDeleteTableDataById}?tableName=${tableName}&keyValue=${id_string}&keyName=${keyName}`;
      this.$axios({
        url: getUrl,
        method: "PUT"
      })
        .then(response => {
          if (response.data.status === '0') {
            layer.msg(`删除成功`,{icon:6})
            this.dialogVisible = false;
            //更新table数据
            this.$emit('delete-search',"2")
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //打印
    printer_click(){
      var projectId = this.projId;
      if (this.checkLength == !1) {
         layer.msg(`请选中一项`,{icon:6})
      }else{

        var id = '';
        if (this.keyValuesType === 3) {//合同打印
          id = this.multipleSelection[0].fileId;
          var  contractType = this.multipleSelection[0].fileType;
          this.editUrl = appendParams(
            `${this.url_a}/eiweb/action/bizObjectBase.action`,
            {
              keyValues : id,
              tableName : "TZ_T_PROJ_CONTRACT_FILE",
              dealMark : "view",
              sequenceName : "SEQ_PROJ_CONTRACT_FILE",
              springJndiId: "YHKG_DS_EI",
              resultJsp : "../WEB-INF/views/projectContract/projectSubFundContractPrint.jsp",
              className : "com.founder.ei.bizobject.ProjectContractObj",
              flag:1,
              projectId : projectId,
              isPrint:"Y",
              PROJ_ID_PI : this.projObject,
              flag:'flag',
              instshow:"show",
              contractType :contractType,
              openMethod : "0"
            }
          );
          window.open(this.editUrl)
          //layerOpenWindow("打印",this.editUrl,'1000px', '600px',this.printProjectPage,)
        }

         if (this.keyValuesType === 4) {//投资支付打印

          id = this.multipleSelection[0].pqId;
          this.editUrl = appendParams(
            `${this.url_a}/eiweb/action/bizObjectBase.action`,
            {
              keyValues : id,
              tableName : "XJL_T_PAYMENT_REQUEST",
              dealMark : "view",
              sequenceName : "",
              springJndiId: "YHKG_DS_EI",
              resultJsp : "../WEB-INF/views/cashflow/printsubFundXjlPay.jsp",
              className : "com.founder.ei.bizobject.xjlPayObj",
              projectXjl:projectId,
              isPrint:"Y",
              openMethod : "0"
            }
          );
          window.open(this.editUrl)
           //layerOpenWindow("打印",this.editUrl,'1000px', '600px',this.printProjectPage,)
        }

        if (this.keyValuesType === 5) {//退出记录打印

          id = this.multipleSelection[0].appId;
          this.editUrl = appendParams(
            `${this.url_a}/eiweb/action/bizObjectBase.action`,
            {
              keyValues : id,
              tableName : "TZ_T_FUND_QUIT_APPL",
              dealMark : "view",
              sequenceName : "SEQ_TZ_T_FUND_QUIT_APPL",
              springJndiId: "YHKG_DS_EI",
              resultJsp : "../WEB-INF/views/fundExit/fundExitApplyPrint.jsp",
              className : "com.founder.ei.bizobject.FundExitApplyObj",
              isPrint:"Y",
              PROJ_OBJECT:this.projObject,
              PROJ_ID:projectId,
              openMethod : "0"
            }
          );
          window.open(this.editUrl)
          //layerOpenWindow("打印",this.editUrl,'1000px', '600px',this.printProjectPage,)
        }


      }
    },
    printProjectPage(){},
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    //当某个单元格被点击时会触发该事件
    //row, column, cell, event
    cellClick(row, column, cell, event){//列表查看
        var toolObj = {
          toobarTitle: "详情",
          openMethod: "2"
        };
        var projectId = this.projId
        var defId,taskId,bizKey,formUri,formUriView

        if(row.instanceModel!==null){
            defId = row.instanceModel.processdefinitionId
            ,taskId = row.instanceModel.taskinstanceId
            ,bizKey = row.instanceModel.bizkey
            ,formUri = row.instanceModel.formuri
            ,formUriView = row.instanceModel.formuriview
        }

        var instId = row.processInstId

        if (this.keyValuesType === 4) {//投资支付
          //参数传递
          var springJndiId = "YHKG_DS_EI"
          ,dealMark ="view"
          ,xmlFileName = "workflowEditorConfig"
          ,tableName = "XJL_T_PAYMENT_REQUEST"
          ,sequenceName = ""
          ,resultJsp = "../WEB-INF/views/cashflow/viewxjlPay.jsp"
          ,className = "com.founder.ei.bizobject.xjlPayObj"

          var id = row.pqId
          if (instId != null && instId != "") {
            var url = appendParams("/eiweb/editor/editor.jsp", {
              dealMark: dealMark,
              showToolBar: false,
              xmlFileName: xmlFileName,
              groupId:2,
              defId: defId,
              taskId: taskId,
              instId: instId,
              bizKey: bizKey,
              formUri: formUri,
              formUriView: formUriView
            });
            layerOpenWindow(toolObj.toobarTitle, url, '850px', '600px', function () {});

          } else {
            var url = appendParams("/eiweb/action/bizObjectBase.action", {
              keyValues: id,
              tableName: tableName,
              dealMark: dealMark,
              xmlFileName: xmlFileName,
              sequenceName: sequenceName,
              springJndiId: springJndiId,
              resultJsp: resultJsp,
              className: className,
              openMethod: toolObj.openMethod
            })
            layerOpenWindow(toolObj.toobarTitle, url, '850px', '600px', function () {});
          }
        }
        if(this.keyValuesType === 2){//质量评估

            var id = row.paramId;
            var url = appendParams("/scweb/editor/editor.jsp", {
              showToolBar: false,
              xmlFileName: "scConfigViewEiF",
              groupId:1,
              isFinish:"Y",
              dealMark: "view",
              keyValues:id,
              instId: instId,
              springJndiId:'YHKG_DS_SC'
            });
            layerOpenWindow(toolObj.toobarTitle, url, '850px', '600px', function () {});
        }
        if(this.keyValuesType === 3){//合同列表
          //参数传递
          var springJndiId = "YHKG_DS_EI"
          ,dealMark ="view"
          ,xmlFileName = "workflowEditorConfig"
          ,tableName = "TZ_T_PROJ_CONTRACT_FILE"
          ,sequenceName = "SEQ_PROJ_CONTRACT_FILE"
          ,resultJsp = "../WEB-INF/views/projectContract/projectSubFundContract.jsp"
          ,className = "com.founder.ei.bizobject.ProjectContractObj"

          var id = row.fileId
          if (instId != null && instId != "") {//合同列表
            var url = appendParams("/eiweb/editor/editor.jsp", {
              dealMark: dealMark,
              showToolBar: false,
              xmlFileName: xmlFileName,
              groupId:2,
              defId: defId,
              taskId: taskId,
              instId: instId,
              bizKey: bizKey,
              PROJ_ID_PI:'',
              formUri: formUri,
              formUriView: formUriView
            });
            layerOpenWindow(toolObj.toobarTitle, url, '850px', '600px', function () {});

          } else {
            var url = appendParams("/eiweb/action/bizObjectBase.action", {
              keyValues: id,
              tableName: tableName,
              dealMark: dealMark,
              sequenceName: sequenceName,
              springJndiId: springJndiId,
              resultJsp: resultJsp,
              className: className,
              projectId:projectId,
              flag:'flag',
              instshow:'show',
              PROJ_ID_PI:'',
              contractType:1,
              openMethod: toolObj.openMethod
            })
            layerOpenWindow(toolObj.toobarTitle, url, '850px', '600px', function () {});
          }
        }
        if(this.keyValuesType === 5){//退出记录
          //参数传递
          var springJndiId = "YHKG_DS_EI"
          ,dealMark ="view"
          ,xmlFileName = "workflowEditorConfig"
          ,tableName = "TZ_T_FUND_QUIT_APPL"
          ,sequenceName = "SEQ_TZ_T_FUND_QUIT_APPL"
          ,resultJsp = "../WEB-INF/views/fundExit/fundExitApply.jsp"
          ,className = "com.founder.ei.bizobject.FundExitApplyObj"

          var id = row.appId
          if (instId != null && instId != "") {
            var url = appendParams("/eiweb/editor/editor.jsp", {
              dealMark: dealMark,
              showToolBar: false,
              xmlFileName: xmlFileName,
              groupId:2,
              defId: defId,
              taskId: taskId,
              instId: instId,
              bizKey: bizKey,
              PROJ_ID_PI:'',
              formUri: formUri,
              formUriView: formUriView
            });
            layerOpenWindow(toolObj.toobarTitle, url, '850px', '600px', function () {});

          } else {
            var url = appendParams("/eiweb/action/bizObjectBase.action", {
              keyValues: id,
              tableName: tableName,
              dealMark: dealMark,
              sequenceName: sequenceName,
              springJndiId: springJndiId,
              resultJsp: resultJsp,
              className: className,
              PROJ_OBJECT:this.projObject,
              PROJ_ID:projectId,
              openMethod: toolObj.openMethod
            })
            layerOpenWindow(toolObj.toobarTitle, url, '850px', '600px', function () {});
          }
        }

       if (this.keyValuesType === 10) {//查看重要事项
          //参数传递
          var springJndiId = "YHKG_DS_EI"
          ,dealMark ="view"
          ,xmlFileName = "workflowEditorConfig"
          ,tableName = "TZ_T_FP_THREEMEETING_INFO"
          ,sequenceName = "SEQ_FP_THREEMEETING_INFO"
          ,resultJsp = "../WEB-INF/views/regularMonitor/threeMettingManage.jsp"
          ,className = "com.founder.ei.bizobject.FpThreeMettingManageObj"

          var id = row.pkId
          if (instId != null && instId != "") {
            var url = appendParams("/eiweb/editor/editor.jsp", {
              dealMark: dealMark,
              showToolBar: false,
              xmlFileName: xmlFileName,
              groupId:2,
              defId: defId,
              taskId: taskId,
              instId: instId,
              bizKey: bizKey,
              formUri: formUri,
              formUriView: formUriView
            });
            layerOpenWindow(toolObj.toobarTitle, url, '850px', '600px', function () {});

          } else {//草稿状态
            var url = appendParams("/eiweb/action/bizObjectBase.action", {
              keyValues: id,
              tableName: tableName,
              dealMark: dealMark,
              sequenceName: sequenceName,
              springJndiId: springJndiId,
              resultJsp: resultJsp,
              className: className,
              fundId:'',
              openMethod: toolObj.openMethod
            })
            layerOpenWindow(toolObj.toobarTitle, url, '850px', '600px', function () {});
          }
        }
    }
  }
};
</script>
<style>
/*表头背景色 */
.el-table th {
  background: #eae3d8 !important;
  color: #997528;
}
.el-table th > .cell {
  font-size: 16px;
}
/*tags*/
.editNav {
  display: flex;
  display: -webkit-flex;
  padding-bottom: 10px;
}
.red {
  background-color: #c50000 !important;
}
.editNav li {
  line-height: 25px;
  padding: 0 10px;
  background-color: #c50000;
  border-radius: 3px;
  list-style: none;
}
.editNav li:nth-child(2) {
  line-height: 25px;
  padding: 0 10px;
  background-color: #dd7772;
  border-radius: 3px;
  list-style: none;
}
.editNav li:nth-child(3) {
  line-height: 25px;
  padding: 0 10px;
  background-color: #dd7772;
  border-radius: 3px;
  list-style: none;
}
.editNav li:nth-child(4) {
  line-height: 25px;
  padding: 0 10px;
  background-color: #dd7772;
  border-radius: 3px;
  list-style: none;
}
.editNav li a {
  font-size: 14px;
  color: #fff;
}
.editNav li i {
  margin-right: 10px;
}
.editNav li + li {
  margin-left: 20px;
}
</style>



