
<template>
<div>
  <v-table :tableDataContent="tableData" :tableOptions="tableOptions" :modelValue="true" :tindex="0" :selectionCheck="1" :tagsModel="true" :projId="projIdA" :keyValuesType="keyValuesType" @delete-search="deleteSearchClick"></v-table>
  <!-- pagination -->
  <div class="paginationNav paginationCommon">
    <div class="title">共 查询到 {{recordsTotal}} 条记录 共 {{totalPage}} 页</div>
    <el-button @click="indexBtn">首页</el-button>
    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" @prev-click="prevClick" @next-click="nextClick" :current-page="currentPage" :total="recordsTotal" :page-size="pageSize" layout="prev, pager, next" background prev-text='上一页'
      next-text='下一页'>
    </el-pagination>
    <el-button @click="backBtn">末页</el-button>
  </div>
</div>
</template>

<script>
import TableCom from '@/components/tableCom'
import url from '@/serviceAPI.config.js'
export default {
  props: {
    projId: {
      type: String,
      default: ''
    },
    tableOptions: {
      type: Array,
      default: []
    },
    ajaxUrl: {
      type: String,
      default: ''
    },
    tindex: {
      type: Boolean
    },
    selectionCheck: {
      type: Boolean
    },
    flag:{
      type:Boolean,
      default:false
    }
  },
  components: {
    "v-table": TableCom
  },
  //数据初始化
  data() {
    return {
      tableData: [], //table数据
      //分页字段
      currentPage: 1, //默认显示第一页
      pageSize: 5, //默认每页显示5条
      totalPage: 0, //总页码
      shouYePage: 1, //首页页码
      recordsTotal: 0, //查询记录数量
      projIdA: '',
      keyValuesType: 4
    }
  },
  created() {
    this.projIdA = this.projId
  },
  watch: {
    flag(val){
        if(val){
           this.getEnteInfos(this.projId, this.pageSize, this.currentPage)
        }
     }
  },
  methods: {
    deleteSearchClick(val) {
      if(val==="1"){//新增-编辑
        this.getEnteInfos(this.projId,this.pageSize, this.currentPage)
      }

      if(val==="2"){//删除
        this.getEnteInfos(this.projId,this.pageSize, 1)
      }

    },
    getEnteInfos(projId, pageSize, currentPage) {
      let _self = this
      var t = new Date().getTime();
      _self.$axios({
          url: `${this.ajaxUrl}?projId=${projId}&pageSize=${pageSize}&currPage=${currentPage}&t=${t}`,
          method: "get"
        })
        .then(response => {
          if (response.status === 200) {
            _self.tableData = response.data.data //获取table数据
            _self.recordsTotal = response.data.totalCount //获取查询总数
            _self.totalPage = response.data.totalPage //获取总页码
          }
        })
        .catch(error => {
          console.log(error)
        })
    },
    // 回到首页
    indexBtn() {
      this.currentPage = this.shouYePage;
      this.getEnteInfos(this.projId, this.pageSize, this.currentPage);
    },
    // 回到末页
    backBtn() {
      this.currentPage = this.totalPage;
      this.getEnteInfos(this.projId, this.pageSize, this.currentPage);
    },
    //上一页
    prevClick(currentPage) {
      this.currentPage = currentPage;
      this.getEnteInfos(this.projId, this.pageSize, currentPage);
    },
    //下一页
    nextClick(currentPage) {
      this.currentPage = currentPage;
      this.getEnteInfos(this.projId, this.pageSize, currentPage);
    },
    handleSizeChange(size) {
      //pageSize 改变时会触发
      let _self = this;
      _self.pageSize = size; //每一页显示多少条
      console.log(`每页 ${size} 条`);
    },
    handleCurrentChange(currentPage) {
      //currentPage 改变时会触发  第几页
      this.getEnteInfos(this.projId, this.pageSize, currentPage);
      this.currentPage = currentPage; //第几页
    }
  },
};
</script>

<style>
/*分页-start*/
.paginationCommon {
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
}
.paginationCommon .title {
  font-size: 12px;
  font-family: "Microsoft YaHei";
  margin-right: 106px;
}
/*button 首页 末页*/
.paginationCommon .el-button {
  padding: 0 10px;
  display: inline-block;
  font-size: 13px;
  min-width: 29px;
  height: 29px;
  line-height: 29px;
  vertical-align: top;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background-color: #fff;
  color: #d1ab62;
  border: 1px solid #d1ab62;
}

paginationCommon .el-button:hover {
  color: #fff;
  background-color: #d1ab62;
}

paginationCommon .el-button:active {
  color: #fff;
  background-color: #d1ab62;
}

paginationCommon .el-button:link {
  color: #fff;
  background-color: #d1ab62;
}

.paginationCommon .el-pagination.is-background .btn-prev,
.el-pagination.is-background .el-pager li {
  background: url(../assets/images/prev_sanjiao.png) no-repeat scroll 7px center;
  border: 1px solid #d1ab62;
}

.paginationCommon .el-pagination.is-background .btn-next,
.el-pagination.is-background .el-pager li {
  background: url(../assets/images/next_sanjiao.png) no-repeat scroll 50px center;
  border: 1px solid #d1ab62;
}

.paginationCommon .el-pagination button,
.el-pagination span:not([class*="suffix"]) {
  padding: 0 10px;
}

.paginationCommon .el-pagination.is-background .el-pager li:not(.disabled).active {
  color: #fff;
  background-color: #d1ab62;
}
/*分页-end*/
</style>



