<template>
  <div>
    <div class="table-P1-view">
      <div class="item_view">
          <div class="item">
              <div class="left">出资主体*</div>
              <div class="right">{{contentData.inveName}}</div>
          </div>
          <div class="item">
              <div class="left">拟投资比例不超过(%)</div>
              <div class="right">{{contentData.perShare}}</div>
          </div>
      </div>
      <div class="item_view">
          <div class="item">
              <div class="left">拟投资金额不超过(元)*</div>
              <div class="right">{{contentData.intendedAmount}} {{contentData.intendedCurrency}}</div>
          </div>
          <div class="item">
              <div class="left">折算为人民币(元)*</div>
              <div class="right">{{contentData.rmbIntendedAmount}} {{contentData.intendedCurrency}}</div>
          </div>
      </div>
      <div class="editItem">
          <div class="left">决策事项* </div>
          <div class="right" v-html="str1"></div>
      </div>
      <div class="editItem">
          <div class="left">咨询委员会成员</div>
          <div class="right">{{contentData.advisoryCommName}}</div>
      </div>
      <div class="editItem">
          <div class="left">决策相关材料</div>
          <div class="right">
            <a v-for="(item,index) in contentData.remarkAttList" :key="index" :href="item.filePath"  v-if="item.fileOldName!==null">{{item.fileOldName}}<span>({{item.fileSize}})</span></a>
          </div>
      </div>
      <div class="editItem">
          <div class="left">决策会议纪要</div>
          <div class="right">
            <a v-for="(item,index) in contentData.decisionAttList" :key="index" :href="item.filePath"  v-if="item.fileOldName!==null">{{item.fileOldName}}<span>({{item.fileSize}})</span></a>
          </div>
      </div>

	</div>
  </div>
</template>

<script>
export default {
  props:{
    contentData:{
      type:Object,
      default: function () {
        return {
          inveName: '',
          perShare:'',
          intendedAmount:'',
          intendedCurrency:'',
          rmbIntendedAmount:'',
          majorMatter:'',
          advisoryCommName:'',
          remarkAttList:'',
          decisionAttList:''
        }
      }
    }
  },
  data() {
    return {}
  },
  computed:{
    str1(){
      if(this.contentData.majorMatter){
          return this.contentData.majorMatter.replace(/\n/g, "<br/>")
      }
    }
  },
  watch: {},
  mounted() {},
  created() {

  },
  methods: {}
};
</script>
<style scoped>
.table-P1-view {
  border: 1px solid #e9e9e9;
}
.table-P1-view .item_view {
  display: flex;
  width: 100%;
}
.table-P1-view .item_view + .item_view {
  border-top: 1px solid #e9e9e9;
}
.table-P1-view .item_view .item {
  display: flex;
  width: 50%;
}
.table-P1-view .item_view .item + .item {
  border-left: 1px solid #e9e9e9;
}
.table-P1-view .item_view .item .left {
  border-right: 1px solid #e9e9e9;
  width: 167px;
  color: #98742a;
  font-size: 14px;
  font-family: 微软雅黑;
  padding-left: 14px;
  box-sizing: border-box;
  background-color: #f4f4f4;
  display: flex;
  align-items: center;
}
.table-P1-view .item_view .item .right {
  color: #666;
  font-size: 14px;
  font-family: 微软雅黑;
  padding-left: 19px;
  box-sizing: border-box;
  width: 64%;
  padding: 16px 20px;
}
.table-P1-view .editItem {
  width: 100%;
  border-top: 1px solid #e9e9e9;
  display: flex;
}
.editItemH {
  height: 94px;
}
.table-P1-view .editItem .left {
  width: 167px;
  background-color: #f4f4f4;
  border-right: 1px solid #e9e9e9;
  color: #98742a;
  font-size: 14px;
  font-family: 微软雅黑;
  padding: 16px 0 16px 14px;
  box-sizing: border-box;
}
.table-P1-view .editItem .right {
  width: 82.3%;
  padding: 16px 20px 16px 20px;
  box-sizing: border-box;
  color: #666;
  font-size: 14px;
  font-family: 微软雅黑;
}
.table-P1-view .editItem .right a{
  color: blue;
  font-size: 14px;
  font-family: 微软雅黑;
}
.table-P1-view .editItem .right a+a{
  margin-left: 10px;
}
.table-P1-view .editItem .right a span{
  color: #666;
  font-size: 14px;
  font-family: 微软雅黑;
}
</style>






