<template>
<div>
  <div v-if="tagsModel">
    <ul class="editNav">
      <li @click="add_new"><a href="javascript:;"><i class="el-icon-circle-plus-outline"></i>新增</a></li>
      <li @click="edit_click" :class="checkLength==1?'red':''"><a href="javascript:;"><i class="el-icon-edit"></i>编辑</a></li>
      <li @click="delete_click" :class="multipleSelection.length!==0?'red':''"><a href="javascript:;"><i class="el-icon-circle-close-outline"></i>删除</a></li>
    </ul>
  </div>
  <el-dialog
  title="提示"
  :visible.sync="dialogVisible"
  width="30%"
  :before-close="handleClose">
  <span>是否删除</span>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="delete_click_ack">确 定</el-button>
  </span>
  </el-dialog>
  <el-table
    v-loading="false"
    :data="tableDataContent"
    border
    style="width:100%;"
    @selection-change="handleSelectionChange"
    >
    <el-table-column v-if="tindex"
      type="index"
      align="center"
      width="55">
    </el-table-column>
    <el-table-column v-if="selectionCheck" type="selection" width="55" align="center"></el-table-column>
    <el-table-column
        header-align="center"
        align="left"
        width="300"
        label="电子版">
      <template slot-scope="scope">
        <a v-for="(item,index) in scope.row.eDocAttList" :key="index" :href="item.filePath" class="column_A" >{{item.fileOldName}}</a>
      </template>
    </el-table-column>
    <el-table-column
        header-align="center"
        align="left"
        width="300"
        label="正式版">
        <template slot-scope="scope">
         <a v-for="(item,index) in scope.row.formVersionAttList" :key="index" :href="item.filePath" class="column_A">{{item.fileOldName}}</a>
        </template>
    </el-table-column>
     <el-table-column
        prop="sheetType"
        header-align="center"
        align="left"
        width="140"
        label="类别">
    </el-table-column>
     <el-table-column
        prop="statusName"
        header-align="center"
        align="left"
        width=""
        label="状态">
    </el-table-column>
  </el-table>
</div>

</template>

<script>
import {
  appendParams,
  layerOpenWindow
} from "@/util/project-submit.js";
import url from "@/serviceAPI.config.js";
export default {
  props: [
    "tableDataContent",
    "tableOptions",
    "tindex",
    "selectionCheck",
    "tagsModel",
    "projId",
    "keyValuesType"
  ],
  components: {},
  data() {
    return {
      multipleSelection: [],
      dialogVisible: false,
      dialogVisible1: false,
      checkLength: 0,
      newPageUrl: "", //新增url
      editUrl: "", //编辑url
      deleteUrl: "", //删除url
      url_a:""//http://172.30.70.166:9089
    };
  },
  created() {
    //这里写得不到数据，应为数据是延迟返回的
  },
  watch: {
    tableDataContent: function(val) {},
    tindex: function(val) {}
  },
  methods: {
    tableheaderClassName({ row, rowIndex }) {
      return "table-head-th";
    },
    handleSelectionChange(val) {
      if (val.length === 1) {
        this.checkLength = 1;
      } else {
        this.checkLength = 0;
      }
      this.multipleSelection = val;
    },
    //新增
    add_new() {
      var _self = this
      var projectId = this.projId,keyValues;

        if (this.keyValuesType === 1) {
          //投前文件管理
          keyValues = "";
          this.newPageUrl = appendParams(
            `${this.url_a}/eiweb/action/bizObjectBase.action`,
            {
              keyValues: keyValues,
              tableName: "TZ_T_TERM_SHEET",
              dealMark: "new",
              sequenceName: "SEQ_TERM_SHEET",
              springJndiId: "YHKG_DS_EI",
              resultJsp:
                "../WEB-INF/views/fundsProjectDecision/addPreInvestFiles.jsp",
              className: "com.founder.ei.bizobject.PreInvestFilesObj",
              projectId: projectId,
              openMethod: "2"
            }
          );
          layerOpenWindow("新增",this.newPageUrl,'1000px', '600px',function(){
              _self.$emit("new-file",'1')
          })
        }
    },
    //编辑
    edit_click() {
      var _self = this
      var projectId = this.projId;
      if (this.checkLength!== 1) {
        layer.msg(`只能选中一项`)
      } else {
        //投前文件管理 --- 编辑
        if (this.multipleSelection[0].status == 0) {
          var id = this.multipleSelection[0].sheetId;
          if (this.keyValuesType === 1) {
            this.editUrl = appendParams(
              `${this.url_a}/eiweb/action/bizObjectBase.action`,
              {
                keyValues: id,
                tableName: "TZ_T_TERM_SHEET",
                dealMark: "update",
                sequenceName: "SEQ_TERM_SHEET",
                springJndiId: "YHKG_DS_EI",
                resultJsp:
                  "../WEB-INF/views/fundsProjectDecision/addPreInvestFiles.jsp",
                className: "com.founder.ei.bizobject.PreInvestFilesObj",
                projectId: projectId,
                openMethod: "2"
              }
            );
            layerOpenWindow("编辑",this.editUrl,'1000px', '600px',function(){
              _self.$emit("new-file",'1')
            })
          }
        } else {
          layer.msg(`只能草稿下才能编辑`)
        }
      }
    },
    //删除
    delete_click() {
        var flag = true

        if(this.multipleSelection.length==0){
          layer.msg(`请先选择一条数据`,{icon:6})
          return
        }
        for(let i=0;i<this.multipleSelection.length;i++){
          if(this.multipleSelection[i].status.indexOf('0')==-1){
              layer.msg(`只能草稿下才能删除`,{icon:6})
              flag = false
          }
        }

        if(flag){
          this.dialogVisible = true;
        }
    },
    delete_click_ack() {
      //删除请求接口
      var projectId = this.projId;
      var id_string
      let objData = this.multipleSelection
      const delete_arr = []
      for(var i in objData) {
        delete_arr.push(objData[i].sheetId)
      }
      id_string = delete_arr.join(',')
      let getUrl = `${
        url.getDeleteTableDataById
      }?tableName=TZ_T_TERM_SHEET&keyValue=${id_string}&keyName=SHEET_ID`;
      this.$axios({
        url: getUrl,
        method: "PUT"
      })
        .then(response => {
          if (response.status === 200) {
            layer.msg(`删除成功`,{icon:6})
            this.dialogVisible = false;
            //更新table数据
            this.$emit('delete-file',"2")
          }
        })
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    }
  }
};
</script>
<style>
/*表头背景色 */
.el-table th {
  background: #eae3d8 !important;
  color: #997528;
}
.el-table th > .cell {
  font-size: 16px;
}
/*url 文件地址*/
.column_A{
  display: block;
}
.column_A{
  color: blue;
}
/*tags*/
.editNav {
  display: flex;
  display: -webkit-flex;
  padding-bottom: 10px;
}
.red {
  background-color: #c50000 !important;
}
.editNav li {
  line-height: 25px;
  padding: 0 10px;
  background-color: #c50000;
  border-radius: 3px;
  list-style: none;
}
.editNav li:nth-child(2) {
  line-height: 25px;
  padding: 0 10px;
  background-color: #dd7772;
  border-radius: 3px;
  list-style: none;
}
.editNav li:nth-child(3) {
  line-height: 25px;
  padding: 0 10px;
  background-color: #dd7772;
  border-radius: 3px;
  list-style: none;
}
.editNav li a {
  font-size: 14px;
  color: #fff;
}
.editNav li i {
  margin-right: 10px;
}
.editNav li + li {
  margin-left: 20px;
}
</style>



