<template>
  <div>
      <div class="filetype" v-if="filetypeName1">
         <div class="left">{{filetypeName1}}：</div>
         <div class="right">
            <ul class="tab-menu">
              <li>
                <ul class="any">
                  <li><a href="###" :class="tabIndex==-1?'changeColor':''" class="tab_item filter-tag" data-itemindex='-1' @click="tabsIndex">不限</a></li>
                </ul>
              </li>
              <li>
                <ul class="list">
                  <li v-for="(item, index) in fileTabsData" :key="index">
                    <a href="###" :class="tabIndex==index?'changeColor':''" class="tab_item filter-tag" :data-itemindex='index' @click="tabsIndex">{{item}}</a>
                  </li>
                </ul>
              </li>
            </ul>
         </div>
      </div>

      <div class="filetype" v-if="filetypeName4">
         <div class="left">{{filetypeName4}}}：</div>
         <div class="right">
            <ul class="tab-menu">
              <li>
                <ul class="any any_w">
                  <li><a href="###" :class="tabStatusMIndex==-1?'changeColor':''" class="tab_item_more filter-tag" data-itemindex='-1' @click="tabsMoreIndex">不限</a></li>
                </ul>
              </li>
              <li>
                <ul class="list" id="status_list_ul">
                  <li v-for="(item, index) in fileTabsMoreData" :key="index">
                    <a href="###" class="tab_item_more filter-tag" data-flag="0" :data-itemindex='index' @click="tabsMoreIndex">{{item}}</a>
                  </li>
                </ul>
              </li>
              <li class="li_more" @click="toggleClick">
                <a v-if="moreModel" href="###">更多<i class="el-icon-caret-bottom"></i></a>
                <a v-else href="###">收起<i class="el-icon-caret-top"></i></a>
              </li>
            </ul>
         </div>
      </div>

      <div class="filetype" v-if="filetypeName2">
         <div class="left">{{filetypeName2}}：</div>
         <div class="right">
            <ul class="tab-menu">
              <li>
                <ul class="any">
                  <li><a href="###" :class="tabStatusIndex==-1?'changeColor':''" class="tab_item_status filter-tag" data-itemindex='-1' @click="tabsStatusIndex">不限</a></li>
                </ul>
              </li>
              <li>
                <ul class="list" id="statusList">
                  <li v-for="(item, index) in fileTabsStatusData" :key="index">
                    <a href="###" :class="tabStatusIndex==index?'changeColor':''" class="tab_item_status filter-tag" :data-itemindex='index' @click="tabsStatusIndex">{{item}}</a>
                  </li>
                </ul>
              </li>
            </ul>
         </div>
      </div>
      <div class="filetype" v-if="filetypeName3">
         <div class="left">{{filetypeName3}}：</div>
         <div class="right filetype_btn">
            <input v-model="input" placeholder="请输入 项目名称 / 基金名称 / 出资主体/被投对象" class="input_text" />
            <ul class="btn_nav">
              <li><a href="###"><i class="el-icon-search"></i>查询</a></li>
              <li><a href="###"><i class="el-icon-delete"></i>清空</a></li>
            </ul>
         </div>
      </div>
  </div>
</template>

<script>
import $ from "jquery"
export default {
  props:['filetypeName1','filetypeName2','filetypeName3','filetypeName4','fileTabsData','fileTabsMoreData','fileTabsStatusData'],
  data() {
    return {
      input: '',
      tabIndex:-1,
      tabStatusIndex:-1,
      tabStatusMIndex:-1,
      moreModel:true
    }
  },
  methods:{
    //类型tabs
    tabsIndex: function (e) {
       this.tabIndex = e.target.dataset.itemindex
       //获取a 集合
       var arr = document.getElementsByClassName("tab_item")
       if(this.tabIndex == e.target.dataset.itemindex){
         if(this.tabIndex ==-1){
            this.$nextTick(function(){
              $('.any .tab_item').removeClass("filter-tag")
            })
         }else{
            let tindex = parseInt(e.target.dataset.itemindex)+1
            this.$nextTick(function(){
              //渲染完毕
              arr[tindex].classList.remove("filter-tag")
            })
         }
       }
    },
    //类型tabs-更多
    tabsMoreIndex: function (e) {
       this.tabStatusMIndex = e.target.dataset.itemindex
       var flag = e.target.dataset.flag
       //获取a 集合
       var arr = document.getElementsByClassName("tab_item_more")
       if(this.tabStatusMIndex == e.target.dataset.itemindex){
         if(this.tabStatusMIndex ==-1){
            this.$nextTick(function(){
              $('.any_w .tab_item_more').removeClass("filter-tag")
              $('#status_list_ul li').find('a').removeClass("changeColor")
            })
         }else{
            let mindex = parseInt(e.target.dataset.itemindex)+1
            //点击多选
            if(flag==0){
              this.$nextTick(function(){
                //渲染完毕
                $('.any_w .tab_item_more').removeClass("changeColor")
                arr[mindex].classList.add("changeColor")
                arr[mindex].classList.remove("filter-tag")
                arr[mindex].setAttribute("data-flag",1)
              })
            }else{
              this.$nextTick(function(){
                //渲染完毕
                $('.any_w .tab_item_more').removeClass("changeColor")
                arr[mindex].classList.add("filter-tag")
                arr[mindex].classList.remove("changeColor")
                arr[mindex].setAttribute("data-flag",0)
              })
            }
         }
       }
    },
    toggleClick:function(){
       if(this.moreModel){
        this.moreModel=false
       }else{
        this.moreModel=true
       }
    },
    //状态tabs
    tabsStatusIndex: function (e) {
      this.tabStatusIndex = e.target.dataset.itemindex
       //获取a 集合
      var arr = document.getElementsByClassName("tab_item_status")
      if(this.tabStatusIndex == e.target.dataset.itemindex){
        if(this.tabStatusIndex ==-1){
          this.$nextTick(function(){
            $('.any .tab_item_status').removeClass("filter-tag")
          })
        }else{
          let tindex = parseInt(e.target.dataset.itemindex)+1
          this.$nextTick(function(){
            arr[tindex].classList.remove("filter-tag")
          })
        }
      }
    },
  }
};
</script>

<style scoped>
.filetype{
  display: flex;
  display: -webkit-flex;
  padding:10px 20px;
}
.filetype .left{
    line-height: 24px;
    height: 24px;
    width: 54px;
}
.filetype .right .tab-menu{
  display: flex;
  display: -webkit-flex;
  padding-left: 40px;
}
.filetype .right .tab-menu li:nth-child(2) .list{
  display: flex;
  display: -webkit-flex;
  flex-wrap: wrap;
}
.filetype .right .tab-menu li:nth-child(1),li:nth-child(2) .list li{
   line-height: 24px;
   height: 24px;
}

.filetype .right .tab-menu .any a{
  color: #000000;
  font-family: "微软雅黑";
  font-size: 12.5px;
  padding: 5px 16px;
  border-radius: 4px 4px 3px 3px;
  text-decoration: none;
}
.filetype .right .tab-menu .list a{
  color: #000000;
  font-family: "微软雅黑";
  font-size: 12.5px;
  padding: 5px 16px;
  border-radius: 4px 4px 3px 3px;
  text-decoration: none;
}

.filetype .right .tab-menu .any .filter-tag:hover {
    color: #333;
    cursor: text;
    background-color: #eff2f4;
}
.filetype .right .tab-menu .list{
  padding-left: 10px;
}
.filetype .right .tab-menu .list .filter-tag:hover {
    color: #333;
    cursor: text;
    background-color: #eff2f4;
}
.changeColor{
  background-color: #EFC17D;
  color: #fff!important;
}

.filetype_btn{
  width: 80%;
  display: flex;
  display: -webkit-flex;
  justify-content:space-between;
}
.btn_nav{
  display: flex;
  display: -webkit-flex;
}
.btn_nav li{
    line-height: 25px;
    line-height: 25px;
    padding: 0 20px;
    background-color: brown;
    border-radius: 3px;
}
.btn_nav li a{
  font-size: 14px;
  color: #fff;
}
.btn_nav li a i{
  margin-right: 5px;
}
.btn_nav li+li{
   margin-left: 16px;
}
.input_text{
  width:258px;
  height: 24px;
  font-size: 12px;
  line-height: 24px;
  border: #d4ccc0 1px solid;
  border-radius: 3px;
  padding-left: 5px;
  vertical-align: middle;
  margin-left: 26px;
}
/*更多*/
.any_w{
  width: 60px;
}
.li_more{
  line-height: 24px;
  height: 24px;
  width: 60px;
}
</style>




