<template>
  <div>
    <div class="table-P1-view">
      <div class="item_view">
          <div class="item">
              <div class="left">子基金简称<span class="requireRed">*</span> </div>
              <div class="right">{{contentData.projObjectName}}</div>
          </div>
          <div class="item">
              <div class="left">项目来源*</div>
              <div class="right">{{contentData.projSrc}} {{contentData.srcDesc}}</div>
          </div>
      </div>
      <div class="item_view">
          <div class="item">
              <div class="left">项目经理* </div>
              <div class="right">{{contentData.mangerName}}</div>
          </div>
          <div class="item">
              <div class="left">项目分管合伙人* </div>
              <div class="right">{{contentData.chargePartner}}</div>
          </div>
      </div>
      <div class="item_view">
          <div class="item">
              <div class="left">项目组成员*  </div>
              <div class="right">{{contentData.memberName}}</div>
          </div>
          <div class="item">
              <div class="left">保密等级* </div>
              <div class="right">{{contentData.secrecyLevel}}</div>
          </div>
      </div>
      <div class="editItem">
          <div class="left">项目概况* </div>
          <div class="right">
            <div class="editContentView" ref="P" v-if="editH">
              <p v-html="str"></p>
              <i class="el-icon-edit iconEdit con-edit"  @click="showEdit"></i>
            </div>
            <div class="editInput" v-if="!this.editH">
                 <el-input
                  type="textarea"
                  autosize
                  placeholder="请输入内容"
                  v-model="contentData.projOverview"
                  >
                </el-input>
            </div>
          </div>
      </div>
	</div>
    <div class="editBtnNav" v-if="!this.editH">
      <div class="button_nav">
          <button @click="onSubmit">保存</button>
          <button @click="onCacel">取消</button>
      </div>
    </div>
  </div>
</template>

<script>
import url from "@/serviceAPI.config.js";
export default {
  props: ["projId"],
  data() {
    return {
      contentData: {
        projObjectName: "",
        projSrc: "",
        mangerName: "",
        chargePartner: "",
        memberName: "",
        secrecyLevel: "",
        projOverview: ""
      },
      editH: true
    };
  },
  computed:{
    str(){
      if(this.contentData.projOverview){
          return this.contentData.projOverview.replace(/\n/g, "<br/>")
      }
    }
  },
  watch: {},
  mounted() {},
  created() {
    this.getBaseInfo(); // 获取项目基本信息
  },
  methods: {
    getBaseInfo() {
      // 获取项目基本信息
      this.$axios({
        url: url.getProjInfoDetail + `/${this.projId}`,
        method: "get"
      })
        .then(response => {

          if (response.data.status === "0") {
            if (response.data.data.model) {
              this.contentData = response.data.data.model;
            }
          }
        })
    },
    showEdit() {//显示输入框
      this.editH = !this.editH
    },
    onSubmit() {
      let _self = this
      if (this.contentData.projOverview == "") {
          layer.msg("请输入内容",{icon:6})
      }else if(this.contentData.projOverview.length>1000){
          layer.msg("请输入1000个字",{icon:6})
      } else {
          //let str = String(this.contentData.projOverview)
          this.updataInfo(this.contentData.projOverview)
      }
    },
    onCacel() {
       this.editH = true
    },
    updataInfo(val){
          if(val){
            const getUrl = `${url.getUpdateTableDataInfo}?tableName=TZ_T_PROJ_INFO&keyName=PROJ_ID&keyValue=${this.projId}&fieldName=PROJ_OVERVIEW&fieldValue=${val}`
            const url1 = encodeURI(getUrl)
            this.$axios({
              url: url1,
              method: "PUT"
            })
              .then(response => {
                if (response.data.status === '0') {
                  layer.msg(`${response.data.msg}`,{icon:6})
                  this.getBaseInfo()
                  this.editH = true
                }
              })

          }
    },
  }
};
</script>
<style scoped>
.requireRed{
  color: red;
}
.table-P1-view {
  border: 1px solid #e9e9e9;
}
.table-P1-view .item_view {
  display: flex;
  width: 100%;
}
.table-P1-view .item_view + .item_view {
  border-top: 1px solid #e9e9e9;
}
.table-P1-view .item_view .item {
  display: flex;
  height: 46px;
  line-height: 46px;
  width: 50%;
}
.table-P1-view .item_view .item + .item {
  border-left: 1px solid #e9e9e9;
}
.table-P1-view .item_view .item .left {
  border-right: 1px solid #e9e9e9;
  width: 167px;
  color: #98742a;
  font-size: 14px;
  font-family: 微软雅黑;
  padding-left: 14px;
  box-sizing: border-box;
  background-color: #f4f4f4;
}
.table-P1-view .item_view .item .right {
  color: #666;
  font-size: 14px;
  font-family: 微软雅黑;
  padding-left: 19px;
  box-sizing: border-box;
  width: 64%;
}
.table-P1-view .editItem {
  width: 100%;
  border-top: 1px solid #e9e9e9;
  display: flex;
}
.editItemH {
  height: 125px;
}
.table-P1-view .editItem .left {
  width: 167px;
  background-color: #f4f4f4;
  border-right: 1px solid #e9e9e9;
  color: #98742a;
  font-size: 14px;
  font-family: 微软雅黑;
  padding-left: 14px;
  padding-top: 16px;
  box-sizing: border-box;
}
.table-P1-view .editItem .right {
  width: 82.3%;
  position: relative;
}
.table-P1-view .editItem .right p {
  padding: 18px 20px;
  color: #666;
  font-size: 14px;
  font-family: 微软雅黑;
}
.table-P1-view .editItem .right i{
  position: absolute;
  right: 7px;
  bottom: 7px;
}
.editBtnNav {
  display: flex;
  justify-content: flex-end;
  margin-top: 8px;
}
.button_nav {
  display: flex;
  display: -webkit-flex;
  margin-top: 5px;
}
.button_nav button {
  width: 77px;
  height: 27px;
  line-height:27px;
  border-radius: 3px;
  border: 0;
  background-color: brown;
  font-size: 14px;
  color: #fff;
  font-family: 微软雅黑;
}

.button_nav button:nth-child(2) {
  background-color: #ebeef5;
  color: #333;
}
.button_nav button + button {
  margin-left: 10px;
}
</style>






