<template>
  <div>
    <div class="table-P1-view">
      <div class="editItem">
          <div class="left">基金名称* </div>
          <div class="right">{{contentData.subFundName}}</div>
      </div>
      <div class="editItem">
          <div class="left">附件 </div>
          <div class="right">
            <a v-for="(item,index) in contentData.rptFileAttList" :key="index" :href="item.filePath"  v-if="item.fileOldName!==null">{{item.fileOldName}}<span>({{item.fileSize}})</span></a>
          </div>
      </div>
      <div class="editItem">
          <div class="left">备注</div>
          <div class="right" v-html="str"></div>
      </div>
	  </div>
  </div>
</template>

<script>
export default {
  props:{
    contentData:{
      type:Object,
      default: function () {
        return {
          subFundName: '',
          rptFileAttList:'',
          keyword:''
        }
      }
    }
  },
  data() {
    return {}
  },
  computed:{
    str(){
      if(this.contentData.keyword){
          return this.contentData.keyword.replace(/\n/g, "<br/>")
      }
    }
  },
  watch: {},
  mounted() {},
  created() {},
  methods: {}
};
</script>
<style scoped>
.table-P1-view {
  border: 1px solid #e9e9e9;
}
.table-P1-view .editItem {
  width: 100%;
  border-top: 1px solid #e9e9e9;
  display: flex;
}
.editItemH {
  height: 94px;
}
.table-P1-view .editItem .left {
  width: 167px;
  background-color: #f4f4f4;
  border-right: 1px solid #e9e9e9;
  color: #98742a;
  font-size: 14px;
  font-family: 微软雅黑;
  padding: 16px 0 16px 14px;
  box-sizing: border-box;
}
.table-P1-view .editItem .right {
  width: 82.3%;
  padding: 16px 20px 16px 20px;
  box-sizing: border-box;
  color: #666;
  font-size: 14px;
  font-family: 微软雅黑;
}
.table-P1-view .editItem .right a{
  color: blue;
  font-size: 14px;
  font-family: 微软雅黑;
}
.table-P1-view .editItem .right a+a{
  margin-left: 10px;
}
.table-P1-view .editItem .right a span{
  color: #666;
  font-size: 14px;
  font-family: 微软雅黑;
}
</style>






