<template>
  <div class="navBox">
      <el-menu
      default-active="基本信息"
      :default-openeds='["项目立项","项目决策","项目执行","项目退出"]'
      class="el-menu-vertical-demo"
      background-color="#f8eee2"
      active-text-color="#b50005"
      >
      <!--遍历menu数据-->
      <el-submenu v-for=" (item,index) in dataMenu" :key="index" :index="item.name">
          <template slot="title">
            <span>{{item.name}}：</span>
          </template>
          <el-menu-item-group v-for=" (list,index1) in item.childList" :key="index1" :index="list.name">
              <el-menu-item :index="list.name">
                <!-- <a :href="'#'+list.name">{{list.name}}</a> -->
                <span @click="jumpInto(list.id)">{{list.name}}</span>
              </el-menu-item>
          </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </div>
</template>
<script>
import url from "@/serviceAPI.config.js";
import {getUrlKey} from "@/util/project-submit.js";
export default {
  props:{
    dataMenu:{
        type: Array,
        // 对象或数组默认值必须从一个工厂函数获取
        default: function () {
          return []
        }
    }
  },
  data() {
    return {
      list: []
    };
  },
  created() {
    var url_projId = getUrlKey("keyValues")

    if(url_projId){
       this.$axios({
          url: url.getAppFuncPermissionList + `?fatherid=5002&projId=${url_projId}`,
          method: "get"
        })
          .then(response => {
            if (response.data.status === '0') {
              this.list = response.data.data
            }
          })
          .catch(error => {
            console.log(error);
          });
    }

  },
  methods: {
      jumpInto(idNo){//导航跳转
         $("html, body").animate({scrollTop: $("#chapter-"+idNo).offset().top-105 }, {duration: 500,easing: "swing"});
      },
  }
};
</script>
<style>
.navBox .el-menu-vertical-demo li .el-submenu__icon-arrow {
  display: none !important;
}
.navBox .el-menu-vertical-demo li .el-submenu__title {
  border-bottom: 2px solid #b50005 !important;
  padding-left: 5px!important;
}
.navBox .el-submenu .el-menu-item {
  min-width: 100px !important;
}
.navBox .el-menu-vertical-demo .el-menu-item {
  padding-left: 20px !important;
  font-size: 13px;
}
.navBox .el-menu-vertical-demo .el-menu-item.is-active {
  background: url(../assets/images/detailStyle5.png) no-repeat;
  background-position: 0px 8px;
  font-weight: bold;
}
.navBox .el-menu-vertical-demo .el-menu-item.is-active a {
  font-weight: bold;
  color: #b50005;
}
.navBox .el-menu-vertical-demo .el-menu-item a {
  color: #333;
  text-decoration: none;
}
.navBox .el-menu-vertical-demo .el-menu-item,
.el-submenu__title {
  height: 28px;
  line-height: 28px;
  padding: 0;
}

.navBox .el-submenu__title {
    height: 28px!important;
    line-height: 28px!important;
}

.navBox .el-menu-vertical-demo .el-menu-item-group__title {
  padding: 0;
}
.navBox {
  padding: 0 7px;
  box-sizing: content-box;
}
.navBox .el-menu {
  border: 0 !important;
}
</style>



