                    this.scroolFlagDec = false
                    //投资决策提交
                    this.getProjDecisionInfo()//投资决策提交-项目决策信息
                    this.getProjDecisionVoteList()//投资决策提交-表决结果信息列表
                    this.getProjDecisionVoteMagInfo()//投资决策提交-决议结果信息列表
                    this.getProjDecisionFixflowTaskList()//投资决策提交-审批信息列表

                    this.$axios({//获取尽职调查报告信息
                      url: url.getDueDiligenceRpt + `/${this.url_project_id}`,
                      method: "get"
                    })
                      .then(response => {
                        if (response.data.status === '0') {
                          this.surveyReport = response.data.data.model
                          this.rptId = response.data.data.model.rptId
                        }
                      })
                }

              }

          }
        }
    },
    //滚动监听END
    menuPage(val,data){//菜单权限显示控制页面显示
      const list = data
      for(let i=0;i<list.length;i++){
        const childList = list[i].childList
        for(let k=0;k<childList.length;k++){
          if(childList[k].id==val){}
        }
      }
    },
    getProjInfoDetail(){// 获取项目基本信息
        var t = new Date().getTime();
        this.$axios({
          url: url.getProjInfoDetail + `/${this.url_project_id}?t=${t}`,
          method: "get"
        })
          .then(response => {
            if (response.data.status === '0') {
              this.flagData = true
              if(response.data.data.model){
                  this.informationContent = response.data.data.model;
                  this.projObjectName = response.data.data.model.projObjectName;
                  this.statusModel = Number(response.data.data.model.projStatus);
                  this.projObject = response.data.data.model.projObject;
                  this.scoreResult =  response.data.data.model.scoreResult;
                  this.groupId = response.data.data.model.groupId
              }

              //改变page title
              // document.title = response.data.data.model.projObjectName
              // 1、scoreResult为null：显示提示信息“请先完成质量评估”，按钮不显示；
              // 2、scoreResult不为null，并且projStatus<5：显示提示信息“请点击【提交申请】按钮提交信息”，显示按钮；
              // 3、scoreResult不为null，并且projStatus>=5，显示决策信息，按钮不显示
              // 4、groupId ！==1   保持
              // groupId==1 显示提示信息“请点击【提交申请】按钮提交信息”
              if(this.groupId !=='1'){
                  if(this.scoreResult==null){
                      this.scoreModel1=false
                      this.scoreModel2=false
                      this.scoreModel3=true
                  }
                  //&&this.scoreResult!==null
                  if(this.statusModel<5){
                      this.scoreModel1=true
                      this.scoreModel2=false
                      this.scoreModel3=true
                      this.scoreTitle='请点击【提交申请】按钮提交信息'
                  }
                  //&&this.scoreResult!==null
                  if(this.statusModel>=5){
                      this.scoreModel1=false
                      this.scoreModel2=true
                      this.scoreModel3=false
                  }
              }

              if(this.groupId=='1'){

                  if(this.statusModel<5){
                      this.scoreModel1=true
                      this.scoreModel2=false
                      this.scoreModel3=true
                      this.scoreTitle='请点击【提交申请】按钮提交信息'

                  }

                  if(this.statusModel>=5){
                      this.scoreModel1=false
                      this.scoreModel2=true
                      this.scoreModel3=false
                  }


              }


            }
          })

    },
    getProjAppInfo(){//获取项目立项信息
          var t = new Date().getTime();
          this.$axios({
            url: url.getProjAppInfo + `/${this.url_project_id}?t=${t}`,
            method: "get"
          })
            .then(response => {
              if (response.data.status === '0') {
                if(response.data.data.model){
                  this.informationContentInfo = response.data.data.model;
                }
              }
            })
    },
    getProjAppVoteList(){//获取立项表决结果信息列表
        var t = new Date().getTime();
        this.$axios({
          url: url.getProjAppVoteList + `?projId=${this.url_project_id}&t=${t}`,
          method: "get"
        })
          .then(response => {
            if (response.data.status === '0') {
              this.ProjAppVoteListTableData = response.data.data;
            }
          })
    },
    getProjAppVoteMagInfo(){//获取立项决议结果信息
        var t = new Date().getTime();
        this.$axios({
          url: url.getProjAppVoteMagInfo + `/${this.url_project_id}?t=${t}`,
          method: "get"
        })
          .then(response => {
            if (response.data.status === '0') {
              this.projAppVoteMagInfoData = response.data.data.model;
            }
          })

    },
    getProjAppFixflowTaskList(){//获取项目立项审批信息列表
        var t = new Date().getTime();
        this.$axios({
          url:
            url.getProjAppFixflowTaskList + `?projId=${this.url_project_id}&t=${t}`,
          method: "get"
        })
          .then(response => {
            if (response.data.status === '0'&&response.data.data.length>0) {
              let dataLength = response.data.data.length;
              if(dataLength===0){
                this.graphMoel = false
              }

              if(dataLength!==0){
                  this.processinstanceIdA = response.data.data[0].processinstanceId; //instId
                  this.processdefinitionIdA = response.data.data[0].processdefinitionId; //defid
                  this.taskinstanceIdA = response.data.data[dataLength - 1].taskinstanceId; //taskId
              }

              this.flowChartData = response.data.data;
            }
          })

    },
    getProjDecisionInfo(){//投资决策提交-项目决策信息
          var t = new Date().getTime();
          this.$axios({
            url: url.getProjDecisionInfo + `/${this.url_project_id}?t=${t}`,
            method: "get"
          })
            .then(response => {
              if (response.data.status === '0') {
                if(response.data.data.model){
                    this.investmentContentData = response.data.data.model
                }

              }
            })

    },
    getProjDecisionVoteList(){//投资决策提交-表决结果信息列表
          var t = new Date().getTime();
          this.$axios({
            url: url.getProjDecisionVoteList + `?projId=${this.url_project_id}&t=${t}`,
            method: "get"
          })
            .then(response => {
              if (response.data.status === '0') {
                this.ProjAppVoteListTableDataA = response.data.data;
              }
            })

    },
    getProjDecisionVoteMagInfo(){//投资决策提交-决议结果信息列表
          var t = new Date().getTime();
          this.$axios({
            url: url.getProjDecisionVoteMagInfo + `/${this.url_project_id}?t=${t}`,
            method: "get"
          })
            .then(response => {
              if (response.data.status === '0') {
                this.projAppVoteMagInfoDataA = response.data.data.model;
              }
            })

    },
    getProjDecisionFixflowTaskList(){//投资决策提交-审批信息列表
        var t = new Date().getTime();
        this.$axios({
          url:
            url.getProjDecisionFixflowTaskList + `?projId=${this.url_project_id}&t=${t}`,
          method: "get"
        })
          .then(response => {
            if (response.data.status === '0') {
              let dataLength = response.data.data.length;

              if(dataLength===0){
                this.graphMoelD = false
              }
              if(dataLength!==0){
                  this.processinstanceId = response.data.data[0].processinstanceId; //instId
                  this.processdefinitionId = response.data.data[0].processdefinitionId; //defid
                  this.taskinstanceId =response.data.data[dataLength - 1].taskinstanceId; //taskId
              }

              this.flowChartDataA = response.data.data;
            }
          })

    },
    add_report_project(){//尽职调查报告-编辑
      var _self = this

      //编辑 projObject
      if(this.rptId!==''&&this.rptId!==null){
         var url = appendParams(
          `/eiweb/action/bizObjectBase.action`,
          {
            keyValues :this.rptId,
            tableName : "TZ_T_DUE_DILIGENCE_RPT",
            dealMark : "update",
            sequenceName : "SEQ_DUE_DILIGENCE_RPT",
            springJndiId: "YHKG_DS_EI",
            resultJsp : "../WEB-INF/views/dueDiligenceSurvey/addDueDiligenceSurvey.jsp",
            className : "com.founder.ei.bizobject.DueDiligenceSurveyObj",
            projectId: this.url_project_id,
            PROJ_OBJECT:this.projObject,
            openMethod:"2"
          }
        );
        layerOpenWindow("编辑", url, '1000px', '600px', _self.getReportDetail)
      }
      //新增
      if(this.rptId==null){
          var url = appendParams(
          `/eiweb/action/bizObjectBase.action`,
          {
            keyValues :'',
            tableName : "TZ_T_DUE_DILIGENCE_RPT",
            dealMark : "new",
            sequenceName : "SEQ_DUE_DILIGENCE_RPT",
            springJndiId: "YHKG_DS_EI",
            resultJsp : "../WEB-INF/views/dueDiligenceSurvey/addDueDiligenceSurvey.jsp",
            className : "com.founder.ei.bizobject.DueDiligenceSurveyObj",
            projectId: this.url_project_id,
            PROJ_OBJECT:this.projObject,
            openMethod:"2"
          }
        );
        layerOpenWindow("编辑", url, '1000px', '600px', _self.getReportDetail)
      }

    },
    getReportDetail(){
        var t = new Date().getTime();

        this.$axios({//获取尽职调查报告信息

              url: url.getDueDiligenceRpt + `/${this.url_project_id}?t=${t}`,
              method: "get"
            })
              .then(response => {
                if (response.data.status === '0') {
                  this.surveyReport = response.data.data.model
                  this.rptId = response.data.data.model.rptId
                }
              })
    },
    click_project(){//立项提交
      var url = appendParams(
        `/eiweb/action/bizObjectBase.action`,
        {
          keyValues : this.url_project_id,
          tableName : "TZ_T_PROJ_INFO",
          dealMark : "update",
          sequenceName : "",
          springJndiId: "YHKG_DS_EI",
          resultJsp : "../WEB-INF/views/funds/updateProjectMsgTogether.jsp",
          className : "com.founder.ei.bizobject.FundsInvestObj",
          frameInd: "2",
          openMethod:"2"
        }
      );
      layerOpenWindow("立项提交", url, '1000px', '600px',this.getProjAppEvent);

    },
    click_jue_pro(){//决策提交
        var url = appendParams(
        `/eiweb/action/bizObjectBase.action`,
        {
          keyValues : this.url_project_id,
          tableName : "TZ_T_PROJ_INFO",
          dealMark : "update",
          sequenceName : "",
          springJndiId: "YHKG_DS_EI",
          resultJsp : "../WEB-INF/views/funds/updateProjectDecisionTogether.jsp",
          className : "com.founder.ei.bizobject.FundInvestDecisonObj",
          frameInd: "0",
          openMethod:"2"
        }
      );

       layerOpenWindow("决策提交", url, '1000px', '600px',this.newProjectEvent);
    },
    getProjAppEvent(){//立项提交关闭后-更新数据
      //更新projStatus -更新数据
      this.getProjInfoDetail()
      //立项申请
      this.getProjAppInfo()//获取项目立项信息
      this.getProjAppVoteList() //获取立项表决结果信息列表
      this.getProjAppVoteMagInfo()//获取立项决议结果信息
      this.getProjAppFixflowTaskList()//获取项目立项审批信息列表
    },
    newProjectEvent(){//决策提交提交关闭后-更新数据
      //更新projStatus -更新数据
      this.getProjInfoDetail()
      //投资决策提交
      this.getProjDecisionInfo()//投资决策提交-项目决策信息
      this.getProjDecisionVoteList()//投资决策提交-表决结果信息列表
      this.getProjDecisionVoteMagInfo()//投资决策提交-决议结果信息列表
      this.getProjDecisionFixflowTaskList()//投资决策提交-审批信息列表

    },
    btnSubmit() {
      editProjInfoTogether();
    },
    //open流程图
    openFlowSubmit(val) {
      //流程图样式
      this.dialogTableVisible = true;
      if(val==1){
        //url 拼接解析
        this.graphUrl = appendParams(
          "/sysweb/action/workflowProcess!flowGraphics.action",
          {
            defId: this.processdefinitionIdA,
            instId: this.processinstanceIdA,
            taskId: this.taskinstanceIdA
          })

      }

      if(val==2){
        //url 拼接解析
        this.graphUrl = appendParams(
          "/sysweb/action/workflowProcess!flowGraphics.action",
          {
            defId: this.processdefinitionId,
            instId: this.processinstanceId,
            taskId: this.taskinstanceId
          })

      }
      layerOpenWindow("查看流程图", this.graphUrl, '800px', '600px', function () {});

    },

  }
};

</script>


