<template>
  <div class="transfer-contanier">
    <div class="inner">
      <div class="tree-left">
          <el-input
            placeholder="输入关键字进行过滤"
            v-model="filterText">
          </el-input>
          <el-tree
            class="filter-tree"
            :data="treeData"
            ref="tree">
          </el-tree>

      </div>
      <div class="list-left">
        2
      </div>
    </div>


  </div>
</template>

<script>
export default {
  props:{

  },
  data(){
    return {
      filterText:'',
      treeData:[{
          id: 1,
          label: '一级 1',
          children: [{
            id: 4,
            label: '二级 1-1',
            children: [{
              id: 9,
              label: '三级 1-1-1'
            }, {
              id: 10,
              label: '三级 1-1-2'
            }]
          }]
        }, {
          id: 2,
          label: '一级 2',
          children: [{
            id: 5,
            label: '二级 2-1'
          }, {
            id: 6,
            label: '二级 2-2'
          }]
        }, {
          id: 3,
          label: '一级 3',
          children: [{
            id: 7,
            label: '二级 3-1'
          }, {
            id: 8,
            label: '二级 3-2'
          }]
        }]
    }
  },
  watch:{},
  created(){},
  methods:{},


}
</script>

<style scoped >
.transfer-contanier .inner{
  display: flex;
}
</style>





