<template>
<div>
  <el-table
     v-loading="false"
    :data="tableDataContent"
    border
    style="width:100%;">
    <el-table-column v-if="true"
      label=""
      type="index"
      align="center"
      width="70">
    </el-table-column>
    <el-table-column v-if="false" type="selection" width="55" align="center"></el-table-column>
    <!-- v-for -->
    <el-table-column
      prop="voterName"
      header-align="center"
      align="center"
      label="步骤名称 "
      width="180">
    </el-table-column>
    <el-table-column
      prop="voteDt"
      header-align="center"
      align="center"
      label="处理人"
      width="180">
    </el-table-column>
    <el-table-column
      prop="voteResult"
      header-align="center"
      align="center"
      label="到达时间 "
      width="140">
    </el-table-column>
    <el-table-column
      prop="voteResult"
      header-align="center"
      align="center"
      label="完成时间"
      width="140">
    </el-table-column>
    <el-table-column
      prop="voteResult"
      header-align="center"
      align="center"
      label="处理结果"
      width="140">
    </el-table-column>
    <el-table-column
      prop="remark"
      header-align="center"
      align="center"
      label="处理意见"
    >
    </el-table-column>
  </el-table>
</div>

</template>

<script>
export default {
  props:["tableDataContent"],
  data() {
    return {
      tableData:[]
    }
  },
  created(){
    //这里写得不到数据，应为数据是延迟返回的
  },
  watch:{
    tableDataContent:function(val){

    }
  },
  methods: {
    tableheaderClassName({ row, rowIndex }) {
      return "table-head-th";
    }
  }
};
</script>
<style>
/*表头背景色 */
.el-table th {
  background: #eae3d8 !important;
  color: #997528;
}
.el-table th>.cell {
  font-size: 16px;
}
</style>



