//弹出layui窗口 - 固定宽度和高度，上下居中
function layerOpenWindow(title, url, width, height,func) {
  var pageHeight = top.document.documentElement.clientHeight - 50;
  var etype = BrowserType(); //判断当前浏览类型
  if (etype == "IE10") {
    pageHeight = top.document.documentElement.clientHeight - 50;
  }
  if (height != "" && height > pageHeight) { //如果页面设置高度大小网页可视区域，则用可视区域高度赋值
    height = pageHeight;
  }
  // var hh = (top.document.documentElement.clientHeight - height) / 2;
  if (etype == "IE10") {
    layer.open({
      type: 2,
      title: title,
      shadeClose: false,
      offset: 'auto',
      shade: [0.8, '#393D49'],
      area: [width, height],
      tips: [1, '#c00'],
      scrollbar: false,
      content: url,
      end: func
    });
  } else {
    layer.open({
      type: 2,
      title: title,
      shadeClose: false,
      offset: 'auto',
      shade: [0.8, '#393D49'],
      area: [width, height],
      tips: [1, '#c00'],
      scrollbar: false,
      content: url,
      end: func
    });
  }
}

