<template>
<div>
  <div v-if="tagsModel">
    <ul class="editNav">
      <li @click="add_new"><a href="javascript:;"><i class="el-icon-circle-plus-outline"></i>新增</a></li>
      <li @click="edit_click" :class="checkLength==1?'red':''"><a href="javascript:;"><i class="el-icon-edit"></i>编辑</a></li>
      <li @click="delete_click" :class="multipleSelection.length!==0?'red':''"><a href="javascript:;"><i class="el-icon-circle-close-outline"></i>删除</a></li>
      <!-- <li v-for="(item, index) in menuDataObj" :key="index"><a href="###"><i :class="item.icon"></i>{{item.itemName}}</a></li> -->
    </ul>
  </div>
  <el-dialog
  title="提示"
  :visible.sync="dialogVisible"
  width="30%"
  :before-close="handleClose">
  <span>是否删除</span>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="delete_click_ack">确 定</el-button>
  </span>
  </el-dialog>
  <el-table
    v-loading="false"
    :data="tableDataContent"
    border
    style="width:100%;"
    @selection-change="handleSelectionChange"
    @cell-click="cellClick"
    >
    <el-table-column v-if="tindex"
      type="index"
      align="center"
      width="55">
    </el-table-column>
    <el-table-column v-if="selectionCheck" type="selection" width="55" align="center"></el-table-column>
    <el-table-column
        prop="assessBy"
        header-align="center"
        align="left"
        width="130"
        label="评估人">
    </el-table-column>
    <el-table-column
        prop="assessDt"
        align="left"
        header-align="center"
        width="130"
        label="评估时间">
    </el-table-column>
     <el-table-column
        prop="sheetType"
        align="left"
        header-align="center"
        width="100"
        label="类别">
    </el-table-column>
    <el-table-column
        prop="assessSummary"
        align="left"
        header-align="center"
        width="200"
        label="评估内容">
    </el-table-column>
    <el-table-column
        align="left"
        header-align="center"
        width=""
        label="附件">
        <template slot-scope="scope">
         <a v-for="(item,index) in scope.row.attaFileList" :key="index" :href="item.filePath" class="column_A" >{{item.fileOldName}}</a>
        </template>
    </el-table-column>
  </el-table>
</div>

</template>

<script>
import {
  appendParams,
  layerCommonOpen,
  layerOpenWindow,
} from "@/util/project-submit.js";
import url from "@/serviceAPI.config.js";
export default {
  props: [
    "tableDataContent",
    "tableOptions",
    "tindex",
    "selectionCheck",
    "tagsModel",
    "projId",
    "keyValuesType"
  ],
  components: {},
  data() {
    return {
      multipleSelection: [],
      dialogVisible: false,
      dialogVisible1: false,
      checkLength: 0,
      newPageUrl: "", //新增url
      editUrl: "", //编辑url
      deleteUrl: "", //删除url
      url_a:"",//http://172.30.70.166:9089
      projObject:""
    };
  },
  created() {
    //这里写得不到数据，应为数据是延迟返回的
    //这里写得不到数据，应为数据是延迟返回的
        // /api/ei-web/projInfoDetail/15481
    this.$axios({// 获取项目基本信息
      url: url.getProjInfoDetail + `/${this.projId}`,
      method: "get"
    })
      .then(response => {
        if (response.data.status === '0') {
          if( response.data.data.model){
            this.projObject = response.data.data.model.projObject;
          }
        }
      })
      .catch(error => {
        console.log(error);
      });
  },
  watch: {
    tableDataContent: function(val) {},
    tindex: function(val) {}
  },
  methods: {
    tableheaderClassName({ row, rowIndex }) {
      return "table-head-th";
    },
    handleSelectionChange(val) {
      if (val.length === 1) {
        this.checkLength = 1;
      } else {
        this.checkLength = 0;
      }
      this.multipleSelection = val;
    },
    //新增
    add_new() {
      var _self = this
      var projectId = this.projId,
        keyValues;
          if (this.keyValuesType === 6) {//退出评估
            this.newPageUrl = appendParams(
              `${this.url_a}/eiweb/action/bizObjectBase.action`,
              {
                keyValues : "",
                tableName : "TZ_T_FP_QUIT_ASSESS",
                dealMark : "new",
                sequenceName : "SEQ_TZ_T_FP_QUIT_ASSESS",
                springJndiId: "YHKG_DS_EI",
                resultJsp : "../WEB-INF/views/fundExit/fundExitEvaluation.jsp",
                className : "com.founder.ei.bizobject.FundExitEvaluationObj",
                PROJ_OBJECT : this.projObject,
                PROJ_ID :projectId,
                openMethod : "2"
              }
            );
            layerOpenWindow("新增",this.newPageUrl,'1000px', '600px',function(){
             _self.$emit('delete-search',"1")
            })

          }
    },
    //编辑
    edit_click() {
      var _self = this
      var projectId = this.projId;
      if (this.checkLength == !1) {
       layer.msg(`只能选中一项`,{icon:6})
      } else {
        //不需要状态判断-任何状态都可以编辑
        if (this.keyValuesType === 6) {
          var id = this.multipleSelection[0].assessId
          this.editUrl = appendParams(
            `${this.url_a}/eiweb/action/bizObjectBase.action`,
            {
              keyValues : id,
              tableName : "TZ_T_FP_QUIT_ASSESS",
              dealMark : "update",
              sequenceName : "",
              springJndiId: "YHKG_DS_EI",
              resultJsp : "../WEB-INF/views/fundExit/fundExitEvaluation.jsp",
              className : "com.founder.ei.bizobject.FundExitEvaluationObj",
              PROJ_OBJECT:this.projObject,
              PROJ_ID:projectId,
              openMethod : "2"
            }
          );
          layerOpenWindow("新增",this.editUrl,'1000px', '600px',function(){
             _self.$emit('delete-search',"1")
          })

        }
      }
    },
    //删除
    delete_click() {
      if(this.multipleSelection.length==0){
          layer.msg(`请先选择一条数据`,{icon:6})
          return
      }
      if(this.keyValuesType === 6){
          this.dialogVisible = true;
      }
    },
    delete_click_ack() {
      //删除请求接口
      var projectId = this.projId;
      var id_string
      let objData = this.multipleSelection
      const delete_arr = []
      for(var i in objData) {
        delete_arr.push(objData[i].assessId)
      }
      id_string = delete_arr.join(',')

      let getUrl = `${
        url.getDeleteTableDataById
      }?tableName=TZ_T_FP_QUIT_ASSESS&keyValue=${id_string}&keyName=ASSESS_ID`;
      this.$axios({
        url: getUrl,
        method: "PUT"
      })
        .then(response => {
          if (response.status === 200) {
             layer.msg(`删除成功`,{icon:6})

            this.dialogVisible = false;
            //更新table数据
            this.$emit('delete-search',"2")
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    //当某个单元格被点击时会触发该事件
    //row, column, cell, event
    cellClick(row, column, cell, event){//列表查看
        console.log(row)
        var toolObj = {
          toobarTitle: "详情",
          openMethod: "2"
        }

        var id = row.assessId
        var url = appendParams("/eiweb/action/bizObjectBase.action", {
          keyValues: id,
          tableName: "TZ_T_FP_QUIT_ASSESS",
          dealMark: "view",
          sequenceName: "",
          springJndiId: "YHKG_DS_EI",
          resultJsp:"../WEB-INF/views/fundExit/fundExitEvaluation.jsp",
          className: "com.founder.ei.bizobject.FundExitEvaluationObj",
          inJCEdit:false,
          PROJ_OBJECT:this.projObject,
          PROJ_ID:this.projId,
          openMethod: toolObj.openMethod
        })
        layerOpenWindow(toolObj.toobarTitle, url, '850px', '600px', function () {});

    }
  }
};
</script>
<style>
/*表头背景色 */
.el-table th {
  background: #eae3d8 !important;
  color: #997528;
}
.el-table th > .cell {
  font-size: 16px;
}
/*url 文件地址*/
.column_A{
  display: block;
}
.column_A{
  color: blue;
}


/*tags*/
.editNav {
  display: flex;
  display: -webkit-flex;
  padding-bottom: 10px;
}
.red {
  background-color: #c50000 !important;
}
.editNav li {
  line-height: 25px;
  padding: 0 10px;
  background-color: #c50000;
  border-radius: 3px;
  list-style: none;
}
.editNav li:nth-child(2) {
  line-height: 25px;
  padding: 0 10px;
  background-color: #dd7772;
  border-radius: 3px;
  list-style: none;
}
.editNav li:nth-child(3) {
  line-height: 25px;
  padding: 0 10px;
  background-color: #dd7772;
  border-radius: 3px;
  list-style: none;
}
.editNav li a {
  font-size: 14px;
  color: #fff;
}
.editNav li i {
  margin-right: 10px;
}
.editNav li + li {
  margin-left: 20px;
}
</style>



