<template>
<div class="ztree-container">
    <div class="custom-tree-container">
      <!-- tree -->
      <div
        class="treeList"
      >
       <el-tree
            class="treeContent"
            :data="treeData"
            :props="defaultProps"
            node-key="itemId"
            :default-expand-all="true"
            :highlight-current="highLight"
            :expand-on-click-node="false"
            @node-click="handleNodeClick"
            ref="tree"
          >
            <!-- 使用 scoped slot -->
            <span class="custom-tree-node" slot-scope="{ node, data }">
              <span>{{ node.label}}</span>
              <!-- 编辑 -->

              <el-input
                v-if="(data.itemId+'1')==nodeID"
                class="input-edit"
                style="width:88%"
                size="mini"
                placeholder="请输入内容"
                autofocus="true"
                @clear="handleClear"
                @keyup.enter.native="editChildenNode"
                v-model="inputValueEdit"
              >
              <i @click="changeEdit" v-if="inputValueEdit!=''" slot="suffix" class="el-input__icon el-icon-check" style="font-size:18px;margin-top:-2px;"></i>
              </el-input>
              <!-- 新增 -->
              <el-input
                type="text"
                v-else-if="(data.itemId+'2')==nodeID"
                class="input-edit-new"
                style="width:88%;"
                size="mini"
                placeholder="请输入内容"
                autofocus="true"
                @clear="handleClear"
                @keyup.enter.native="editChildenNode"
                v-model="inputValueEdit"
              >
                <i @click="changeEditNew" v-if="inputValueEdit!=''" slot="suffix" class="el-input__icon el-icon-check" style="font-size:18px;margin-top:-2px;"></i>
                <i @click="changeEditClose" v-else slot="suffix" class="el-input__icon el-icon-close" style="font-size:18px;margin-top:-2px;"></i>
              </el-input>
              <span class="iconNav" v-else>
                <i class="el-icon-edit"  @click="() => append(data,'1')" v-if="data.editable=='0'"></i>
                <i class="el-icon-circle-plus"  @click="() => append(data,'2')"></i>
                <i class="el-icon-delete"  @click="() => remove(node, data)" v-if="data.editable=='0'"></i>
              </span>
            </span>
          </el-tree>
      </div>
      <!-- table -->
      <div class="table-right">
          <ul class="ztree-menu editNav" style="margin-top:10px;">
            <li @click="add_new"><a href="javascript:void(0)"><i class="el-icon-circle-plus-outline"></i>新增</a></li>
            <li @click="edit_click" :class="multipleSelection.length==1?'red':''" style="display:none;">
              <a href="javascript:void(0)"><i class="el-icon-edit"></i>编辑</a>
            </li>
            <li @click="batch_download" :class="multipleSelection.length>=1?'red':''" style="display:none;">
              <a id="download-btn" href="javascript:void(0)"><i class="el-icon-document"></i>批量下载</a>
            </li>
            <li @click="operating_record ">
              <a href="javascript:void(0)"><i class="el-icon-document"></i>操作记录</a>
            </li>
          </ul>
          <div class="button-wrapper">
            <div class="demo-input-suffix title">
              <span style="width:75px;">关键字：</span>
              <el-input
                placeholder="标题/上传人"
                size="small"
                v-model.trim="inputValue">
              </el-input>
            </div>
            <el-row  style="padding-left:30px;">
              <el-button  type="primary" size="small" style="background-color: #b50005;" @click="query">查询</el-button>
              <!-- <el-button  type="primary" size="small" style="background-color: #b50005;" >操作记录</el-button> -->
            </el-row>
          </div>
          <div class="table-box">
              <!-- table -->
              <el-table
                :data="tableData"
                border
                @selection-change="handleSelectionChange"
                v-loading="loading"
                style="width:100%">
                <el-table-column
                type="selection"
                width="55"
                align="center"
                :selectable="checkSelectable"
                ></el-table-column>
                <el-table-column
                  prop="date"
                  label="标题"
                  width="">
                    <template slot-scope="scope">
                      <p>
                        <img :src="imgFolder" alt="" style="width:32px;height:32px;" v-if="scope.row.fileType=='dic'">
                        <img :src="imgFile" alt="" style="width:32px;height:32px;" v-if="scope.row.fileType=='txt'">
                        <img :src="imgFilePng" alt="" style="width:32px;height:32px;" v-if="scope.row.fileType=='png'">
                        <img :src="imgFileJpg" alt="" style="width:32px;height:32px;" v-if="scope.row.fileType=='jpg'">
                        <img :src="imgFileRar" alt="" style="width:32px;height:32px;" v-if="scope.row.fileType=='rar'">
                        <img :src="imgFilePPT" alt="" style="width:32px;height:32px;" v-if="scope.row.fileType=='ppt'">
                        <img :src="imgPPTX" alt="" style="width:32px;height:32px;" v-if="scope.row.fileType=='pptx'">
                        <img :src="imgWordFile" alt="" style="width:32px;height:32px;" v-if="scope.row.fileType=='word'">
                        <img :src="imgPDF" alt="" style="width:32px;height:32px;" v-if="scope.row.fileType=='pdf'">
                        <img :src="imgXlsx" alt="" style="width:32px;height:32px;" v-if="scope.row.fileType=='xlsx' || scope.row.fileType=='XLS' || scope.row.fileType=='xlsm'">
                        <img :src="imgDocx" alt="" style="width:32px;height:32px;" v-if="scope.row.fileType=='docx' || scope.row.fileType=='doc' || scope.row.fileType=='DOCX'">
                        <img :src="imgHtml" alt="" style="width:32px;height:32px;" v-if="scope.row.fileType=='html'">
                        {{scope.row.attaFileName}}
                      </p>

                    </template>
                </el-table-column>
                <el-table-column
                  prop="attaType"
                  width="90"
                  label="类别">
                </el-table-column>
                <el-table-column
                  prop="createName"
                  width="100"
                  label="上传人">
                </el-table-column>
                <el-table-column
                  prop="uplodeDate"
                  width="100"
                  label="创建时间">

                </el-table-column>
                <el-table-column
                  width="140"
                  label="操作">
                    <template slot-scope="scope">
                      <div class="a_nav">
                        <a :href="scope.row.attaFileUrl" :download="scope.row.attaFileName" class="xia_zai tool_cursor" v-if="scope.row.isFile=='0'">下载</a>
                        <a href="javascript:void(0)" @click="row_delete(scope.row)" class="xia_zai tool_cursor" v-if="scope.row.editable=='0'" >删除</a>
                      </div>
                    </template>
                </el-table-column>
              </el-table>
              <!-- 分页组件 -->
              <div class="paginationNav paginationCommon">
                <div class="title">共 查询到 {{recordsTotal}} 条记录 共 {{totalPage}} 页</div>
                <div class="first-page" @click="indexBtn">首页</div>
                <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" @prev-click="prevClick" @next-click="nextClick" :current-page="currentPage" :total="recordsTotal" :page-size="pageSize" layout="prev, pager, next" background prev-text='上一页'
                  next-text='下一页'>
                </el-pagination>
                <div class="end-page" @click="backBtn">末页</div>
              </div>
          </div>
      </div>
    </div>
</div>

</template>
<!--
//---编辑节点
根据editable判断是否可编辑和删除，
1：不可编辑或删除，只能加子节点；
0：可以编辑或删除
//---itemType附件类型
1企业、2基金、3子基金

//isFile 1:文件夹 0 文件

//新增或编辑的url，需要加两个参数：nodeId（选中的树节点）、parentId（选中的树节点的父节点）
-->
<script>
import Scroll from './scroll'
import axios from "axios";
import {getUrlKey,layerOpenWindow,appendParams} from "@/util/project-submit.js";
export default {
  props:{
    contextPath: { //请求地址
      type: String,
      default: "/ei-web/attaItemList"
    },
    flag:{
      type:Boolean,
      default:false
    }
  },
  components: {
    Scroll
  },
  data() {
    return {
      imgFolder:require('@/assets/images/file.png'),
      imgFile:require('@/assets/images/file1.png'),
      imgFilePng:require('@/assets/images/filePng.png'),
      imgFileJpg:require('@/assets/images/file-jpg.png'),
      imgFileRar:require('@/assets/images/fileRar.png'),
      imgFilePPT:require('@/assets/images/file-ppt-iocn.png'),
      imgWordFile:require('@/assets/images/wordFile.png'),
      imgPDF:require('@/assets/images/filePDF.png'),
      imgXlsx:require('@/assets/images/file-xlsx.png'),
      imgDocx:require('@/assets/images/file-word-iocn.png'),
      imgHtml:require('@/assets/images/fileHtml.png'),
      imgPPTX:require('@/assets/images/file-pptx.png'),
      inputValue:'',
      inputValueEdit:"",
      nodeID:'',
      highLight:true,
      cnodeType:'',
      itemNodeId:"",
      default_nodeId:'',//默认
      node_parentId:'0',//item-父节点

      //新增节点
      node_itemName:"",
      node_itemFundId:"",
      node_itemParentId:"",
      tableData: [],
      treeData:[],
      loading:false,
      defaultProps: {
        children: "childrenList",
        label: "itemName",
      },
      multipleSelection: [],//table-Select集合
      //分页字段
      currentPage: 1, //默认显示第一页
      pageSize:10, //默认每页显示5条
      totalPage: 0, //总页码
      shouYePage: 1, //首页页码
      recordsTotal: 0, //查询记录数量
      url_id:''
    };
  },
  created() {
    this.url_id = getUrlKey("keyValues")
  },
  watch: {
    flag(val){
      if(val){
          this.updataTree()
      }
    }
  },
  methods: {
    updataTree(){//获取tree-data
      const _self = this
      const pageType = "3"
      const t = new Date().getTime();
      const Url = `${this.contextPath}?projOrFundId=${this.url_id}&itemType=${pageType}&t=${t}`
      axios({
        url:Url,
        method: "get"
      }).then(function(response) {
        if (response.data.status === "0" ) {

          const arrFirst = response.data.data[0].childrenList

          _self.itemNodeId = arrFirst[0].itemId
          const default_nodeId = arrFirst[0].itemId

          let nodeLight = _self.$refs.tree.getCurrentKey()
          if(nodeLight==null){
            _self.getTableData(default_nodeId)
          }else{
            _self.getTableData(nodeLight)
          }
          //渲染tree
          _self.treeData = response.data.data

        }
      })
    },
    handleSelectionChange(val) {//选中check节点
      this.multipleSelection = val;
    },
    add_new(){//新增
      const _self = this;
      const keyValues = "";
      const pageType = "3"
      const newPageUrl = appendParams(
        `/eiweb/action/bizObjectBase.action`,
        {
          keyValues: keyValues,
          tableName: "BD_T_FUND_ATTA",
          dealMark: "new",
          sequenceName: "SEQ_BD_T_FUND_ATTA",
          springJndiId: "YHKG_DS_EI",
          resultJsp:"../WEB-INF/views/financingManage/dealFundAtta.jsp",
          className: "com.founder.ei.bizobject.FundAttaObj",
          fundId: this.url_id,
          openMethod: "2",
          nodeId:this.itemNodeId,
          itemType:pageType
        }
      );
      layerOpenWindow("新增",newPageUrl,'600px', '350px',function(){
        _self.updataTree()
      })
    },
    edit_click(){//编辑
      const _self = this;

      if (this.multipleSelection.length !=1) {
        layer.msg(`请选中一项`,{icon:6})
      } else if(this.multipleSelection[0].editable=="1"){
        layer.msg(`该选项不可编辑,请重新选择`,{icon:6})
      }else{
              const id = this.multipleSelection[0].attaId;
              const editUrl = appendParams(
                `/eiweb/action/bizObjectBase.action`,
                {
                  keyValues: id,
                  tableName: "BD_T_FUND_ATTA",
                  dealMark: "update",
                  sequenceName: "",
                  springJndiId: "YHKG_DS_EI",
                  resultJsp:"../WEB-INF/views/financingManage/dealFundAtta.jsp",
                  className: "com.founder.ei.bizobject.FundAttaObj",
                  openMethod: "2",
                  nodeId:this.itemNodeId,
                  parentId:this.node_parentId
                }
              );
              layerOpenWindow("编辑",editUrl,'600px', '350px',function(){
                _self.getTableData(_self.itemNodeId,_self.pageSize,_self.currentPage)
              })
      }
    },
    batch_download(){//批量下载
      const _self = this;
      if (this.multipleSelection.length==0) {
        layer.msg(`请选中一项`,{icon:6})
      } else{
        const arrFileUrl = []
        for(let item of this.multipleSelection){
          const itemObject={
            url:item.attaFileUrl,
            name:item.attaFileName
          }
          arrFileUrl.push(item.attaFileUrl)
        }

        var btn = document.getElementById('download-btn');

        //将要进行多文件下载的mp3文件地址，以组数的形式存起来
        function download(name, href) {
            var a = document.createElement("a"), //创建a标签
                e = document.createEvent("MouseEvents"); //创建鼠标事件对象
            e.initEvent("click", false, false); //初始化事件对象
            a.href = href; //设置下载地址
            a.download = name; //设置下载文件名
            a.dispatchEvent(e); //给指定的元素，执行事件click事件
        }

        //给多文件下载按钮添加点击事件
        btn.onclick = function name(params) {
            for (let index = 0; index < arrFileUrl.length; index++) {
                download('第' + index + '个文件', arrFileUrl[index]);
            }
        }
      }
    },
    operating_record(){//操作记录
        const recordUrl = appendParams(
          `/eiweb/action/showListview.action`,
          {
            listViewFileName:"fundAttaDetailView.xml",
            listViewId: "detailListId",
            fromFilter: "FUND_ID='"+this.url_id+"'",
            fundId:this.url_id
          }
        );
        layerOpenWindow("操作记录",recordUrl,'1000px', '600px',function(){})
    },
    getTableData(nodeId,pageSize=10, currentPage=1){//获取table-data
      const t = new Date().getTime();
      this.$axios({
          url: `/ei-web/fileList?length=${pageSize}&currPage=${currentPage}&nodeId=${nodeId}&fundId=${this.url_id}&itemType=3&t=${t}`,
          method: "get"
        })
        .then(response => {
          if (response.status === 200) {

            this.tableData = response.data.data //获取table数据
            this.recordsTotal = response.data.totalCount //获取查询总数
            this.totalPage = response.data.totalPage //获取总页码
            this.loading = false
            let nodeLight = this.$refs.tree.getCurrentKey()
            if(nodeLight==null){
              this.$refs.tree.setCurrentKey(this.itemNodeId)
            }else{
              //节点高亮
              this.$refs.tree.setCurrentKey(nodeLight)
            }

          }
        })
    },
    query(){//查询
      const t = new Date().getTime();
      //参数
      const projId      = this.url_id
      const pageSize    = this.pageSize
      const currentPage = this.currentPage
      const keyWord     = this.inputValue

      const nodeId = this.$refs.tree.getCurrentKey()
      // if(keyWord==''){
      //    return layer.msg("请输入内容",{icon:6})
      // }
      // this.loading = true
      if(keyWord.length>10){
        return layer.msg("请输入小于10",{icon:6})
      }

      this.$axios({
        url: `/ei-web/fileList?length=${pageSize}&currPage=${currentPage}&nodeId=${nodeId}&fundId=${projId}&keyWord=${keyWord}&itemType=3&t=${t}`,
        method: "get"
      })
      .then(response => {
        if (response.status === 200) {
          this.tableData = response.data.data //获取table数据
          this.recordsTotal = response.data.totalCount //获取查询总数
          this.totalPage = response.data.totalPage //获取总页码
          this.loading = false
        }
      })
    },
    //input 失去焦点处理start
    handleBlur(){
      this.nodeID = ''
      this.highLight = true
    },

    //input 失去焦点处理End
    changeEdit(){//切换edit
      if(this.inputValueEdit.length>10) return layer.msg("请输入小于10个字",{icon:6})
      this.changeNode();
      this.nodeID = ''
      this.highLight = true
    },

     changeEditNew(){//切换edit
      if(this.inputValueEdit.length>10) return layer.msg("请输入小于10个字",{icon:6})
      this.newNode();
      this.nodeID = ''
      this.highLight = true
    },

    changeEditClose(){
      this.nodeID = ''
      this.highLight = true
    },
    handleClear(){//清空clear
    },
    handleChange(value){

    },
    handleNodeClick(data) {//点击node节点
      this.node_parentId =  data.parentId//存入父节点信息
      this.itemNodeId   = data.itemId//存入子节点信息

      const pageSize    = this.pageSize
      const currentPage = this.currentPage

      this.getTableData(this.itemNodeId,pageSize,currentPage)
    },
    append(data,type) {//添加节点
      this.highLight = false
      this.cnodeType = type

      if(type=="1"){
        this.inputValueEdit = data.itemName
        //控制input显示与否
        this.nodeID = data.itemId+"1"
      }

      if(type=="2"){
        this.inputValueEdit = ''
        //控制input显示与否
        this.nodeID = data.itemId+"2"
      }

      //新增编辑节点
      this.node_itemFundId = data.projectOrFundId
      this.node_itemParentId = data.itemId
    },
    editChildenNode(){//输入框按下enter键触发事件
      if(this.inputValueEdit==""){
        layer.msg("请输入内容",{icon:6})
      }else{


        if(this.cnodeType=="1"){
          this.changeNode();
        }
        if(this.cnodeType=="2"){//新增节点
          if(this.inputValueEdit.length>10) return layer.msg("请输入小于10个字",{icon:6})
          this.newNode();
        }

        this.highLight = true
      }
    },
    newNode(){//改变节点-新增
        let _self = this

        let Url = `/ei-web/attaItemAdd?itemName=${this.inputValueEdit}&fundId=${this.node_itemFundId}&parentId=${this.node_itemParentId}&itemType=3`
        axios({
          url:Url,
          method: "post"
        }).then(function(response) {
          if (response.data.status === "0" ) {
            _self.updataTree()
            _self.nodeID = ''
            _self.inputValueEdit = ''
            const msg = response.data.msg
            layer.msg(msg,{icon:6})

          }
        })
    },
    changeNode(){//改变节点信息
      let _self = this
      let Url = `/ei-web/attaItemUpdate/${this.node_itemParentId}?itemName=${this.inputValueEdit}`
      axios({
        url:Url,
        method: "PUT"
      }).then(function(response) {
        if (response.data.status === "0" ) {
          _self.updataTree()
          _self.nodeID = ''
          _self.inputValueEdit = ''
          const msg = response.data.msg
          layer.msg(msg,{icon:6})
        }
      })
    },
    remove(node, data) {//删除节点
      const parent = node.parent;
      const id = data.itemId
      this.$confirm('此操作将删除该节点且同时删除列表里对应文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          //需要对数据库进行删除操作
          let _self = this
          let Url = `/ei-web/attaItemDelete/${id}`
          axios({
            url:Url,
            method: "DELETE"
          }).then(function(response) {
            if (response.data.status === "0" ) {
              _self.updataTree()
              layer.msg("删除成功!",{icon:6})
            }
          })

        }).catch(() => {
          layer.msg("已取消删除",{icon:6})
      });


    },
    row_delete(row){

      const _self = this
      const id = row.attaId
      const itemID = this.$refs.tree.getCurrentKey()

      let Url = `/ei-web/deleteTableDataById?tableName=BD_T_FUND_ATTA&keyName=ATTA_ID&keyValue=${id}`
      axios({
        url:Url,
        method: "PUT"
      }).then(function(response) {
        if (response.data.status === "0" ) {
          const msg = response.data.msg
          layer.msg(msg,{icon:6})
          //更新table-data
          const pageSize    = _self.pageSize
          const currentPage = _self.currentPage
          //更新table-data数据
          _self.getTableData(itemID,pageSize,currentPage)
        }
      })
    },
    checkSelectable(row, index){//判断CheckBox 是否可以勾选
      if (row.editable=='1')
        return 0;//不可勾选
      else
        return 1;//可勾选
    },
    indexBtn() {// 回到首页
      this.currentPage = this.shouYePage;
      this.getTableData(this.itemNodeId,this.pageSize,this.currentPage)
    },
    backBtn() {// 回到末页
      this.currentPage = this.totalPage;
      this.getTableData(this.itemNodeId,this.pageSize,this.currentPage)
    },

    prevClick(currentPage) {//上一页
      this.currentPage = currentPage;
      this.getTableData(this.itemNodeId,this.pageSize,this.currentPage)
    },

    nextClick(currentPage) {//下一页
      this.currentPage = currentPage;
      this.getTableData(this.itemNodeId,this.pageSize,this.currentPage)
    },
    handleSizeChange(size) { //pageSize 改变时会触发
      this.pageSize = size; //每一页显示多少条
    },
    handleCurrentChange(currentPage) {//currentPage 改变时会触发  第几页
      this.currentPage = currentPage; //第几页
      this.getTableData(this.itemNodeId,this.pageSize,this.currentPage)
    }

  }
};
</script>

<style>
.custom-tree-node {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
  padding-right: 8px;
}
.xia_zai{
  display: block;
  width: 50px;
  height: 29px;
  line-height: 29px;
  background-color: rgb(181, 0, 5);
  border-radius: 4px;
  text-align: center;
  color: #fff;
  font-size: 14px;
}
.xia_zai:hover{
  color: #fff;
}
.a_nav{
  display: flex;
}
.a_nav a+a{
  margin-left: 4px;
}
/*
  工具条
*/
.iconNav{
  display: none;
}
.treeList .el-tree-node__content:hover .iconNav{
   display: inline-block;
}
.treeList .el-tree--highlight-current .el-tree-node.is-current>.el-tree-node__content:hover{
   padding-right: 10px;
}
/* .treeList .el-tree--highlight-current .el-tree-node.is-current>.el-tree-node__content:hover .iconNav{
   display: inline-block;
} */
.treeList .el-tree--highlight-current .el-tree-node.is-current>.el-tree-node__content {
    background-color: #d1ab62;
    color: #fff;
    padding-right:10px;
}
.treeList .el-tree--highlight-current .el-tree-node.is-current>.el-tree-node__content .iconNav{
   display: inline-block;
}
.tool_cursor{
  cursor: pointer;
}
/*tags*/
.editNav {
  display: flex;
  display: -webkit-flex;
  padding-bottom: 10px;
}
.red {
  background-color: #c50000 !important;
}
.editNav li {
  line-height: 25px;
  padding: 0 10px;
  background-color: #c50000;
  border-radius: 3px;
  list-style: none;
}
.editNav li:nth-child(2) {
  line-height: 25px;
  padding: 0 10px;
  background-color: #dd7772;
  border-radius: 3px;
  list-style: none;
}
.editNav li:nth-child(3) {
  line-height: 25px;
  padding: 0 10px;
  background-color: #dd7772;
  border-radius: 3px;
  list-style: none;
}
.ztree-container .editNav li:nth-child(4) {
  line-height: 25px;
  padding: 0 10px;
  background-color: #c50000;
  border-radius: 3px;
  list-style: none;
}
.editNav li a {
  font-size: 14px;
  color: #fff;
}
.editNav li i {
  margin-right: 10px;
}
.editNav li + li {
  margin-left: 20px;
}
/*tagsEnd*/

.custom-tree-container {
  display: flex;
  border: 1px solid #ccc;
  /* overflow: hidden; */
}
.custom-tree-container .el-tree-node__content{
  position: relative;
  margin-bottom:8px;
}
/*input 输入*/
.custom-tree-node .input-edit{
  position: absolute;
  top: 1px;
  left: 20px;
}
.custom-tree-node .input-edit-new{
  position: absolute;
  top: 3px;
  left: 12px;
}
.custom-tree-node .el-input--mini .el-input__inner {
    height: 23px;
    line-height: 23px;
}
.custom-tree-container .treeList{
  /* margin-top: -32px; */
  box-sizing: border-box;
  /* padding-right: 60px; */
  width: 250px;
  height: auto;
  border-right: 1px solid #ccc;
  overflow-y: hidden;
  overflow-x: auto;
}
/*处理横向滚动问题*/

.el-tree {
    min-width: 100%;
    display:inline-block !important;
}
/*处理横向滚动问题End*/

.custom-tree-container .button-wrapper {
  display: flex;
  align-items: center;
  height: 40px;
}

/*right*/
.table-right {
  padding: 10px;
  min-width: 70%;
}
.custom-tree-container .button-wrapper .title {
  display: flex;
}
/* .custom-tree-container .button-wrapper .title span {
  width: 86px;
  padding-top: 10px;
  font-size: 16px;
} */

/* .custom-tree-container  .el-button--primary:focus, .el-button--primary:hover {
    background: #66b1ff!important;
} */



.paginationCommon .el-pagination.is-background .btn-prev,
.el-pagination.is-background .el-pager li {
  background: url(../../assets/images/prev_sanjiao.png) no-repeat scroll 7px center;
  border: 1px solid #d1ab62;
}
.paginationCommon .el-pagination.is-background .btn-next,
.el-pagination.is-background .el-pager li {
  background: url(../../assets/images/next_sanjiao.png) no-repeat scroll 50px center;
  border: 1px solid #d1ab62;
}
.paginationCommon {
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
}
.paginationCommon .title {
  font-size: 12px;
  font-family: "Microsoft YaHei";
  margin-right: 106px;
}
.paginationCommon  .el-pagination button,
.paginationCommon  .el-pagination span:not([class*="suffix"]) {
    padding: 0 10px;
}
.paginationCommon .el-pagination.is-background .el-pager li:not(.disabled).active {
  color: #fff;
  background-color: #d1ab62;
}
/*button 首页 末页*/
.first-page,.end-page {
  padding: 0 10px;
  display: inline-block;
  font-size: 13px;
  min-width: 29px;
  height: 29px;
  line-height: 29px;
  vertical-align: top;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background-color: #fff;
  color: #d1ab62;
  border: 1px solid #d1ab62;
}
.first-page:hover {
  color: #fff;
  background-color: #d1ab62;
}
.end-page:hover {
  color: #fff;
  background-color: #d1ab62;
}
</style>



